import React from 'react';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { startFetchingTwoGisSuggests, finishFetchingTwoGisSuggests } from 'store/actions/twoGisSuggests';
import { TwoGisSuggestFetchingStatus } from 'store/reducers/data/twoGisSuggestsData';
import { getSuggestsList } from 'tools/twoGis/getSuggestsList';

export const useAddressSimpleUpdateSuggests = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const latitude = useSelector(({ userData }) => userData.deviceLocationLatitude);
    const longitude = useSelector(({ userData }) => userData.deviceLocationLongitude);
    const suggestsFetchingStatus = useSelector(({ twoGisSuggestsData }) => twoGisSuggestsData.fetchingStatus);

    React.useEffect(() => {
        if (suggestsFetchingStatus !== TwoGisSuggestFetchingStatus.INITIAL) {
            return;
        }

        dispatch(startFetchingTwoGisSuggests());

        getSuggestsList({ q: inputValue, locale: 'ru_RU', latitude, longitude })
            .then((items) => {
                dispatch(finishFetchingTwoGisSuggests(items));
            })
            .catch(() => {
                dispatch(finishFetchingTwoGisSuggests([]));
            });
    }, [dispatch, inputValue, latitude, longitude, suggestsFetchingStatus]);
};
