import { GradientFade } from 'components/GradientFade/GradientFade';
import { PlasmaIcon } from 'components/PlasmaIcon/PlasmaIcon';
import { BodyText1 } from 'components/Text/Body';
import { FootnoteText1 } from 'components/Text/Footnote';
import { HeadlineText1 } from 'components/Text/Headline';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import React from 'react';
import { color } from 'style/const/color';
import styled from 'styled-components';

const circleContainerSize = 316;
const plasmaIconSize = 48;
const offsetToCenteringIcon = circleContainerSize / 2 - plasmaIconSize / 2;
const StyledPlasmaIcon = styled(PlasmaIcon)`
    position: absolute;
    height: ${plasmaIconSize}px;
    width: ${plasmaIconSize}px;
    top: ${offsetToCenteringIcon}px;
    left: ${offsetToCenteringIcon}px;
`;

export const StyledCircles = styled.div`
    position: relative;
    display: flex;
    justify-content: center;
    align-items: center;
    height: ${circleContainerSize}px;
    width: ${circleContainerSize}px;
    margin-top: 106px;

    &::after {
        position: absolute;
        top: 86px;
        left: 86px;
        right: 86px;
        bottom: 86px;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: rgba(255, 255, 255, 0.06);
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;

        opacity: 0.3;
        background: rgba(255, 255, 255, 0.02);
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

const StyledOkButton = styled(FootnoteText1)`
    position: relative;
    display: inline-flex;
    height: 78px;
    width: 78px;
    justify-content: center;
    align-items: center;
    color: ${color.text.primary};
    margin-left: 12px;

    &::before {
        position: absolute;
        top: 0;
        left: 0;
        right: 0;
        bottom: 0;

        content: '';
        border-radius: 50%;
        z-index: -1;

        opacity: 0.3;
        background: ${color.surface.liquid1};
        box-shadow: inset 0 4px 4px rgba(255, 255, 255, 0.15);
        backdrop-filter: blur(48px);
    }
`;

export const GuidePageRoot = styled.div`
    height: 100%;
    display: flex;
    flex-direction: column;
    align-items: center;
`;

export const GuidePagePlasmaIcon = () => (
    <GradientFade sizePx={50} isVisible kind="bottom">
        <StyledCircles>
            <StyledPlasmaIcon />
        </StyledCircles>
    </GradientFade>
);

export const GuidePageTitle = styled(BodyText1)`
    margin-top: -54px;
    color: ${color.text.primary};
`;

export const GuidePageSource = styled(HeadlineText1)`
    position: relative;
    margin-top: 32px;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;
`;

export const GuidePageSubtitle = styled(BodyText1)`
    margin-top: 130px;
    color: ${color.text.secondary};
`;

interface GuidePageHintProps {
    onOkClick: () => void;
}
export const GuidePageHint: React.FC<GuidePageHintProps> = ({ onOkClick }) => {
    const okButtonRef = useFocusOnMount<HTMLDivElement>();

    return (
        <GuidePageSubtitle>
            Чтобы ввести с клавиатуры, нажмите на пульте кнопку
            <StyledOkButton ref={okButtonRef} tabIndex={0} onClick={onOkClick}>
                ОК
            </StyledOkButton>
        </GuidePageSubtitle>
    );
};
