const isStatusCode4xx = (statusCode: number) => statusCode >= 400 && statusCode <= 499;

export const doRequest = async <Result = unknown>(
    url: string,
    params: Record<string, string | number | boolean>,
): Promise<Result> => {
    const paramsString = Object.keys(params)
        .reduce<string[]>((acc, key) => {
            acc.push(`${key}=${params[key as keyof typeof params]}`);

            return acc;
        }, [])
        .join('&');

    const response = await fetch(`${url}?${paramsString}`);

    // здесь выбрасываем только сетевые и серверные ошибки
    if (!response.ok && !isStatusCode4xx(response.status)) {
        throw response.status;
    }

    return response.json() as Promise<Result>;
};
