import React from 'react';
import { useKeyboardExpanded } from 'hooks/useKeyboardExpanded';

export const useKeyboardClose = (onKeyboardClose: () => void) => {
    // eslint-disable-next-line @typescript-eslint/no-unused-vars
    const [_, keyboardExpandedCount] = useKeyboardExpanded();

    React.useEffect(() => {
        // ловим момент второго ресайза экрана, а именно, закрытия клавиатуры
        if (keyboardExpandedCount <= 1) {
            return;
        }

        onKeyboardClose();
    }, [keyboardExpandedCount, onKeyboardClose]);
};
