import React, { useEffect, useState } from 'react';
import styled from 'styled-components';
import { useSprings, animated } from 'react-spring/web';
import { useGesture } from 'react-use-gesture';

const StyledSliderContainer = styled.div`
    width: 100%;
    height: 100%;
`;

const StyledSlider = styled.div`
    position: relative;
    overflow: hidden;
    width: 100%;
    height: 100%;
`;

const StyledSlide = styled.div`
    width: 100%;
    height: 100%;
    will-change: transform;
    user-select: none;
    pointer-events: none;
`;

const clamp = (input: number, lower: number, upper: number) => Math.min(Math.max(input, lower), upper);

interface SliderProps {
    auto?: number;
    className?: string;
    children?: React.ReactNode[];
    onSlideChange?: (slide: number) => void;
    width: number;
}

export const Slider: React.FC<SliderProps> = ({ auto = 0, className, children = [], onSlideChange, width }) => {
    const [slide, setSlide] = useState(0);
    const [isDragging, setDragging] = useState(false);

    const [springProps, setSpringProps] = useSprings(children.length, (index) => ({
        offset: index,
    }));

    const gestureBinds = useGesture(
        {
            onDrag: ({ down, movement: [xDelta], direction: [xDir], distance, cancel, first }) => {
                if (first) {
                    setDragging(true);
                }

                if (down && distance > width / 2) {
                    if (cancel) cancel();

                    setSlide(clamp(slide + (xDir > 0 ? -1 : 1), 0, children?.length - 1));
                }

                setSpringProps((index: number) => ({
                    offset: (down ? xDelta : 0) / width + (index - slide),
                }));
            },
            onClick: () => {
                if (isDragging) {
                    setDragging(false);
                }

                return false;
            },
        },
        {
            drag: {
                delay: 200,
            },
        },
    );

    useEffect(() => {
        setSpringProps((index: number) => ({ offset: index - slide }));

        onSlideChange && onSlideChange(slide);
    }, [slide, setSpringProps, onSlideChange]);

    useEffect(() => {
        let intervalId: number;

        if (auto > 0) {
            intervalId = setInterval(() => {
                const targetIndex = (slide + 1) % children.length;

                setSlide(targetIndex);
            }, auto);
        }

        return () => {
            if (intervalId) {
                clearInterval(intervalId);
            }
        };
    }, [auto, children.length, slide]);

    return (
        <StyledSliderContainer className={className}>
            <StyledSlider>
                {springProps.map(({ offset }, index) => (
                    <animated.div
                        {...gestureBinds()}
                        key={index}
                        style={{
                            transform: offset.to((offsetX) => `translateX(${offsetX * 100}%)`),
                            position: 'absolute',
                            width: '100%',
                            height: '100%',
                            willChange: 'transform',
                        }}
                    >
                        <StyledSlide>{children[index]}</StyledSlide>
                    </animated.div>
                ))}
            </StyledSlider>
        </StyledSliderContainer>
    );
};
