import React from 'react';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { closeAppAction } from 'store/actions/app';
import {
    DialogPage,
    DialogTitle,
    DialogButton,
    FullscreenDecoration,
    DialogText,
} from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import emptySvg from './StatusEmpty.assets/empty.svg';

const StyledDecoration = styled(FullscreenDecoration)`
    background-image: url('${emptySvg}');
`;

export const StatusEmpty: React.FC = () => {
    const dispatch = useDispatch();

    const buttons = React.useMemo(
        () => [
            <DialogButton
                shouldFocusOnMount
                view={ButtonViews.secondary}
                onClick={() => {
                    dispatch(closeAppAction());
                }}
            >
                Не сейчас
            </DialogButton>,
        ],
        [dispatch],
    );

    const decorations = React.useMemo(() => <StyledDecoration />, []);

    return (
        <DialogPage buttons={buttons} decorations={decorations}>
            <DialogTitle>
                <MultilineText text="Заказов пока нет" />
            </DialogTitle>
            <DialogText>
                <MultilineText text="Чтобы сделать заказ, скажите «Хочу попкорн»" />
            </DialogText>
        </DialogPage>
    );
};
