export type DGLocale =
    | 'ru_RU' // русский (Россия),
    | 'ru_UA' // русский(Украина),
    | 'ru_KG' // русский(Казахстан),
    | 'ru_KZ' // русский(Киргизия),
    | 'ru_UZ' // русский(Узбекистан),
    | 'uk_RU' // украинский(Россия),
    | 'uk_UA' // украинский(Украина),
    | 'en_RU' // английский(Россия),
    | 'en_AE' // английский(ОАЭ),
    | 'en_CY' // английский(Кипр),
    | 'it_RU' // итальянский(Россия),
    | 'it_IT' // итальянский(Россия),
    | 'es_RU' // испанский(Россия),
    | 'es_CL' // испанский(Кипр),
    | 'ar_AE' // арабский(ОАЭ),
    | 'ar_RU' // арабский(Россия),
    | 'cs_CZ' // чешский(Чехия),
    | 'cs_RU' // чешский(Россия),
    | 'az_AZ'; // азербайджанский (Азербайджан).

interface DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
    };
}

export interface DGErrorResponse extends DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
        error: {
            type: string;
            message: string;
        };
    };
}

export interface DGSuccessResponse<ResponseType> extends DGBaseResponse {
    meta: {
        code: number;
        api_version: string;
        issue_date: string;
    };
    result: ResponseType;
}

export type DGApiResponse<ResponseType> = DGSuccessResponse<ResponseType> | DGErrorResponse;

export const isErrorResponse = (response: DGApiResponse<unknown>): response is DGErrorResponse =>
    'error' in response.meta;

export interface DGListResult<Item> {
    total: number;
    items: Item[];
}

export interface DGLatLng {
    lat: number;
    lon: number;
}
