import { AppReducer, initialState } from 'store/constants';
import {
    LastOrderPosition,
    OrderStateProductItem,
    Price,
    ParsedPrice,
    DiscountedPrice,
    ParsedDiscountedPrice,
} from 'store/constants/orderStatus';

const parsePrice = ({ value, ccy: currency }: Price): ParsedPrice => ({
    value: parseInt(value, 10) / 100,
    currency,
});

const parseDiscountedPrice = ({ discountValue, ...price }: DiscountedPrice): ParsedDiscountedPrice => ({
    ...parsePrice(price),
    discountValue: parseInt(discountValue, 10) / 100,
});

const getOrderStateProductItem = (position: LastOrderPosition): OrderStateProductItem => ({
    ...position,
    originalPrice: parseDiscountedPrice(position.originalPrice),
    originalTotalPrice: parseDiscountedPrice(position.originalTotalPrice),
    originalQuantity: parseInt(position.originalQuantity, 10),
    formedPrice: parseDiscountedPrice(position.formedPrice),
    formedQuantity: parseInt(position.formedQuantity, 10),
    formedTotalPrice: parseDiscountedPrice(position.formedTotalPrice),
    imageUrl: position.images_urls[0],
});

const parseBoolean = (value: string): boolean => value === 'False';

export const orderStatusData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'LAST_ORDER_INFO': {
            const payload = action.payload.info;

            state.orderStatusData = {
                ...initialState.orderStatusData,
                productList: payload.positions.map(getOrderStateProductItem),
                orderDate: new Date(payload.expectedDateTime),
                orderStatus: payload.orderStatus,
                orderId: payload.orderId,
                comment: payload.comment !== 'None' ? payload.comment : '',
                isChanged: parseBoolean(payload.changedOnFormed),
                deliveryPrice: parseDiscountedPrice(payload.deliveryPrice),
                orderDiscount: parsePrice(payload.orderDiscount),
                originalTotalPrice: parseDiscountedPrice(payload.originalTotalPrice),
                originalOrderPrice: parsePrice(payload.originalOrderPrice),
                formedTotalPrice: parseDiscountedPrice(payload.formedTotalPrice),
                formedOrderPrice: parsePrice(payload.formedOrderPrice),
            };

            break;
        }

        case 'LAST_ORDER_EMPTY':
        case 'LAST_ORDER_ERROR':
        case 'ASK_FOR_LAST_ORDER_INFO': {
            state.orderStatusData = { ...initialState.orderStatusData };

            break;
        }

        default:
            break;
    }
};
