import React from 'react';
import styled from 'styled-components';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { HeadlineText2 } from 'components/Text/Headline';
import { color } from 'style/const/color';

export const CartListContainer = styled.div`
    position: relative;

    height: 792px;
    width: 832px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

const preventScroll = (e: React.UIEvent<HTMLDivElement>) => {
    e.currentTarget.scrollTop = 0;
};

const StyledScrollerContainer = styled.div`
    position: relative;

    margin-bottom: 56px;

    max-height: 600px;
    overflow: hidden;

    --spatial-navigation-contain: contain;
`;

const StyledScrollerWrapper = styled.div`
    overflow-x: hidden;

    will-change: transform;
    transition: transform 0.5s ease-in-out;
`;

interface CartListScrollerProps {
    topGradientVisible: boolean;
    bottomGradientVisible: boolean;
    wrapperYShift: number;
}

export const CartListScroller: React.FC<CartListScrollerProps> = ({
    children,
    topGradientVisible,
    bottomGradientVisible,
    wrapperYShift,
}) => {
    const wrapperStyle = React.useMemo(
        () => ({
            transform: `translateY(${-1 * wrapperYShift}px)`,
        }),
        [wrapperYShift],
    );

    return (
        <GradientFade sizePx={160} kind="top" isVisible={topGradientVisible}>
            <GradientFade sizePx={160} kind="bottom" isVisible={bottomGradientVisible}>
                <StyledScrollerContainer onScroll={preventScroll}>
                    <StyledScrollerWrapper style={wrapperStyle}>{children}</StyledScrollerWrapper>
                </StyledScrollerContainer>
            </GradientFade>
        </GradientFade>
    );
};

export const CartTotalPrice = styled(HeadlineText2)`
    padding-left: 16px;

    color: ${color.text.primary};
`;
