import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { removeItemAction, addItemAction } from 'store/actions/native';
import { cartSelectItemAction } from 'store/actions/cartPage';
import { Button, ButtonViews } from 'components/Button/Button';
import { LabelProps } from 'components/Label/Label';
import { BodyText2 } from 'components/Text/Body';
import { CartListItemRoot, CartListItemThumb, CartListItemInfo } from 'components/Cart/CartListItem';
import { icon } from 'style/const/icon';
import { color } from 'style/const/color';
import { transition } from 'style/const/transition';
import { focusBorderMixin } from 'style/mixin/focus';
import addIcon from 'assets/icons/add.svg';
import removeIcon from 'assets/icons/remove.svg';

const StyledSelector = styled.div`
    box-sizing: border-box;
    padding: 8px;
    width: 216px;

    display: flex;
    justify-content: space-between;
    align-items: center;
`;

interface StyledSelectorValue {
    isWarning?: boolean;
}
const StyledSelectorValue = styled(BodyText2)<StyledSelectorValue>`
    color: ${({ isWarning }) => (isWarning ? color.text.warning : color.text.primary)};
`;

type SelectorButtonIconType = 'less' | 'more' | 'remove';

const iconTypeIconMap: Record<SelectorButtonIconType, string> = {
    less: removeIcon,
    more: addIcon,
    remove: addIcon,
};

const selectorButtonSizePx = 64;
const selectorBorderRadiusPx = selectorButtonSizePx / 2;
const selectorIconSizePx = icon.s.sizePx;

interface StyledSelectorButtonProps {
    iconType: SelectorButtonIconType;
}
const StyledSelectorButton = styled.button<StyledSelectorButtonProps>`
    position: relative;

    height: ${selectorButtonSizePx}px;
    width: ${selectorButtonSizePx}px;
    border: none;
    border-radius: ${selectorBorderRadiusPx}px;

    display: flex;
    align-items: center;
    justify-content: center;

    background: ${color.button.secondary1};

    will-change: background-color, transform, opacity;
    transition: ${transition.default};

    &::before {
        content: '';

        height: ${selectorIconSizePx}px;
        width: ${selectorIconSizePx}px;

        background-repeat: no-repeat;
        background-size: contain;
        background-position: center;
    }

    &[disabled] {
        opacity: 0.3;
    }

    ${focusBorderMixin({ borderRadius: selectorBorderRadiusPx })}

    &::before {
        background-image: url('${({ iconType }) => iconTypeIconMap[iconType]}');
    }

    ${({ iconType }) =>
        iconType === 'remove' &&
        css`
            transform: rotate(45deg);
            background: ${color.button.critical};
        `}
`;

const StyledDeleteButton = styled(Button)`
    padding: 20px 32px;
`;

export interface CartListItemProps {
    id: string;
    index: number;
}

export interface CartListItemViewProps {
    imageUrl: string;
    name: string;
    price: number;
    oldPrice?: number;
    count: number;
    isWantMoreThanHave?: boolean;
    disabled?: boolean;
    label?: LabelProps;
    isMoreButtonDisabled?: boolean;
}

export const CartListItem: React.FC<CartListItemProps> = ({ index, id }) => {
    const {
        imageUrl,
        name,
        price,
        oldPrice,
        label,
        count,
        isWantMoreThanHave,
        disabled,
        isMoreButtonDisabled,
    } = useSelector(({ cartPage: { itemsMap } }) => itemsMap[id]);
    const dispatch = useDispatch();

    const handleLessClick = useDebouncedOnceFn(
        () => {
            dispatch(
                removeItemAction({
                    item: id,
                    count: '1',
                    shouldRemoveMention: !count,
                }),
            );
        },
        debounceTimeoutMs,
        [dispatch, id, count],
    );

    const handleMoreClick = useDebouncedOnceFn(
        () => {
            dispatch(
                addItemAction({
                    item: id,
                    count: '1',
                }),
            );
        },
        debounceTimeoutMs,
        [dispatch, id],
    );

    const handleRemoveClick = useDebouncedOnceFn(
        () => {
            dispatch(
                removeItemAction({
                    item: id,
                    count: String(Infinity),
                    shouldRemoveMention: true,
                }),
            );
        },
        debounceTimeoutMs,
        [dispatch, id, count],
    );

    const handleFocus = React.useCallback(() => dispatch(cartSelectItemAction(id)), [dispatch, id]);

    return (
        <CartListItemRoot disabled={disabled}>
            <CartListItemThumb imageUrl={imageUrl} index={index} />
            <CartListItemInfo disabled={disabled} label={label} name={name} price={price} oldPrice={oldPrice} />
            {disabled ? (
                <StyledDeleteButton view={ButtonViews.secondary} onFocus={handleFocus} onClick={handleRemoveClick}>
                    Удалить
                </StyledDeleteButton>
            ) : (
                <StyledSelector>
                    <StyledSelectorButton
                        type="button"
                        aria-label="less"
                        iconType={count ? 'less' : 'remove'}
                        onFocus={handleFocus}
                        onClick={handleLessClick}
                    />
                    <StyledSelectorValue isWarning={isWantMoreThanHave}>{count}</StyledSelectorValue>
                    <StyledSelectorButton
                        type="button"
                        aria-label="more"
                        iconType="more"
                        disabled={isMoreButtonDisabled}
                        onFocus={handleFocus}
                        onClick={handleMoreClick}
                    />
                </StyledSelector>
            )}
        </CartListItemRoot>
    );
};
