/* eslint-disable @typescript-eslint/camelcase */
import { doDGApiRequest } from './doApiRequest';
import { DGLocale, DGListResult, DGLatLng } from './common';

type DGGetSuggestsParams = {
    locale: DGLocale;
    q: string;
    fields: string[];
    suggestType: string;
    type: string[];
    polygon?: string;
    viewpoint1?: [string, string];
    viewpoint2?: [string, string];
    location: [string, string];
};

interface DGApiGetSuggestsParams {
    locale: DGLocale;
    q: string;
    fields: string;
    suggest_type: string;
    type: string;
    polygon?: string;
    viewpoint1?: string;
    viewpoint2?: string;
    location: string;
}

export interface DGSuggest {
    id: string;
    full_name: string;
    name: string;
    search_attributes: {
        suggested_text: string;
    };
    type: string;
    point?: DGLatLng;
}

interface DGBuildingAddressComponent {
    number: string;
    street: string;
    street_id: string;
    type: 'street_number';
}

interface DGBuildingAddress {
    building_id: string;
    components?: DGBuildingAddressComponent[];
    postcode: string;
}

export interface DGBuildingSuggest extends DGSuggest {
    type: 'building';
    address_name: string; // Кутузовский проспект, 43 ст2
    purpose_name: string; // Жилой дом с административными помещениями
    address: DGBuildingAddress;
    point: DGLatLng;
}

export const hasAddressComponents = (suggest: DGBuildingSuggest) => {
    // prettier-ignore
    return (
        suggest.address.components &&
        suggest.address.components.length
    );
};

const convertParams = ({
    fields,
    type,
    suggestType,
    location,
    viewpoint1,
    viewpoint2,
    ...params
}: DGGetSuggestsParams): DGApiGetSuggestsParams => ({
    ...params,
    fields: fields.join(','),
    type: type.join(','),
    suggest_type: suggestType,
    location: location.join(','),
    ...(viewpoint1 &&
        viewpoint2 && {
            viewpoint1: viewpoint1.join(','),
            viewpoint2: viewpoint2.join(','),
        }),
});

const suggestsEndpointPath = '/3.0/suggests';

export const getSuggests = async (params: DGGetSuggestsParams) => {
    return doDGApiRequest<DGApiGetSuggestsParams, DGListResult<DGBuildingSuggest>>(
        suggestsEndpointPath,
        convertParams(params),
    );
};
