import { MultilineText } from 'components/MultilineText/MultilineText';
import React from 'react';
import { goToAddressFullAllThunk } from 'store/thunks/native';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { makePurchaseAction } from 'store/actions/server';
import { CartInfoPin, CartInfoContainer, CartPersonal, CartAddress } from 'components/Cart/CartInfo';
import { Button, ButtonViews } from 'components/Button/Button';
import { ParagraphText1 } from 'components/Text/Paragraph';
import { color } from 'style/const/color';

const StyledAskPersonal = styled(ParagraphText1)`
    margin-top: 14px;

    text-align: center;

    color: ${color.text.secondary};
`;

const CartInfoButton = styled(Button)`
    margin-top: 60px;
    padding: 20px 32px;

    &[disabled] {
        opacity: 0.3;
    }
`;

const askPersonal = 'Чтобы сделать заказ, укажите номер телефона, \nквартиру и другую дополнительную \nинформацию';

export const CartInfo: React.FC = () => {
    const dispatch = useDispatch();
    const { address, customerName } = useSelector(({ userData }) => userData);
    const {
        deliveryTime,
        isMakePurchaseButtonDisabled,
        makePurchaseButtonView,
        hasRequiredData,
        phoneNumber,
        personalAddress,
    } = useSelector(({ cartPage }) => cartPage);
    const handlePurchaseClick = useDebouncedOnceFn(() => dispatch(makePurchaseAction()), debounceTimeoutMs, [dispatch]);

    const handleSetAddressClick = useDebouncedOnceFn(
        () => {
            dispatch(goToAddressFullAllThunk());
        },
        debounceTimeoutMs,
        [dispatch],
    );

    return (
        <CartInfoContainer>
            <CartInfoPin time={deliveryTime} />
            <CartAddress>{address}</CartAddress>
            {hasRequiredData ? (
                <>
                    <CartPersonal address={personalAddress} phone={phoneNumber} name={customerName} />
                    <CartInfoButton
                        view={makePurchaseButtonView}
                        disabled={isMakePurchaseButtonDisabled}
                        shouldFocusOnMount
                        shouldLockAfterClick
                        onClick={handlePurchaseClick}
                    >
                        Перейти к оплате
                    </CartInfoButton>
                </>
            ) : (
                <>
                    <StyledAskPersonal>
                        <MultilineText text={askPersonal} />
                    </StyledAskPersonal>
                    <CartInfoButton view={ButtonViews.primary} shouldFocusOnMount onClick={handleSetAddressClick}>
                        Продолжить
                    </CartInfoButton>
                </>
            )}
        </CartInfoContainer>
    );
};
