import { useKeyboardClose } from 'hooks/useKeyboardClose';
import React from 'react';
import { addressFullValidationOkThunk } from 'store/thunks/addressFullPage';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { addressFullPhoneNumberInputChangeAction } from 'store/actions/addressFullPage';
import { Input } from 'components/Input/Input';
import { Label } from 'components/Label/Label';

const StyledContainer = styled.div`
    position: relative;

    margin: 80px auto 0;

    width: 900px;
`;

const StyledLabel = styled(Label)`
    position: absolute;

    margin-top: 12px;

    width: 100%;
    padding: 0 32px;
`;

export const AddressFullPhone = () => {
    const dispatch = useDispatch();
    const inputValue = useSelector(({ addressFullPage }) => addressFullPage.phoneNumberInputValue);
    const isPhoneValid = useSelector(({ addressFullPage }) => addressFullPage.isPhoneValid);
    const [isFirstTry, setFirstTry] = React.useState(true);
    const isErrorLabelVisible = !isPhoneValid && !isFirstTry;

    const handleInputChange = React.useCallback(
        ({ nativeEvent }: React.ChangeEvent<HTMLInputElement>) => {
            // без этого тайпгварда TS не хочет показывать правильные поля
            if (nativeEvent instanceof InputEvent) {
                const { inputType, data } = nativeEvent;

                dispatch(addressFullPhoneNumberInputChangeAction({ inputType, data }));
            }
        },
        [dispatch],
    );
    const handleKeyboardClose = React.useCallback(() => {
        if (isPhoneValid) {
            dispatch(addressFullValidationOkThunk());
        } else {
            setFirstTry(false);
        }
    }, [dispatch, isPhoneValid]);

    useKeyboardClose(handleKeyboardClose);

    return (
        <StyledContainer>
            <Input title="Номер телефона" value={inputValue} shouldFocusInputOnMount onChange={handleInputChange} />
            {isErrorLabelVisible ? <StyledLabel type="critical" content="Неверно введен номер" /> : null}
        </StyledContainer>
    );
};
