import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { OrderStatusCategory } from 'store/constants/orderStatus';
import { Wait } from 'pages/Wait/Wait';
import {
    DialogPage,
    DialogTitle,
    DialogButton,
    FullscreenDecoration,
    DialogText,
} from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { ButtonViews } from 'components/Button/Button';

import pendingSvg from './StatusSummary.assets/pending.svg';
import deliveringSvg from './StatusSummary.assets/delivering.svg';
import successSvg from './StatusSummary.assets/success.svg';
import failedSvg from './StatusSummary.assets/failed.svg';

const mapCategoryToBackground: Record<OrderStatusCategory, string> = {
    [OrderStatusCategory.PENDING]: `url('${pendingSvg}')`,
    [OrderStatusCategory.DELIVERING]: `url('${deliveringSvg}')`,
    [OrderStatusCategory.SUCCESS]: `url('${successSvg}')`,
    [OrderStatusCategory.FAILED]: `url('${failedSvg}')`,
};

interface StyledDecorationProps {
    type: OrderStatusCategory;
}
const StyledDecoration = styled(FullscreenDecoration)<StyledDecorationProps>`
    background-image: ${({ type }) => mapCategoryToBackground[type]};
`;

export const StatusSummary: React.FC = () => {
    const dispatch = useDispatch();

    const isLoaded = useSelector((state) => state.orderStatusSummaryPage.isLoaded);
    const title = useSelector((state) => state.orderStatusSummaryPage.title);
    const text = useSelector((state) => state.orderStatusSummaryPage.text);
    const orderStatusCategory = useSelector((state) => state.orderStatusSummaryPage.orderStatusCategory);

    const isFailed = orderStatusCategory === OrderStatusCategory.FAILED;

    const buttons: React.ReactNode = React.useMemo(() => {
        const result = [
            <DialogButton
                shouldFocusOnMount={!isFailed}
                key="dialog-button-details"
                onClick={() => {
                    dispatch(push(ROUTES.STATUS_DETAILS));
                }}
            >
                Детали заказа
            </DialogButton>,
            <DialogButton
                shouldFocusOnMount={isFailed}
                key="dialog-button-report"
                view={isFailed ? ButtonViews.primary : ButtonViews.secondary}
                onClick={() => {
                    dispatch(push(ROUTES.STATUS_REPORT));
                }}
            >
                Связаться с нами
            </DialogButton>,
        ];

        return isFailed ? result.reverse() : result;
    }, [dispatch, isFailed]);

    const decorations = React.useMemo(() => <StyledDecoration type={orderStatusCategory} />, [orderStatusCategory]);

    if (!isLoaded) {
        // TODO: Заменить на скелет https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-243
        return <Wait />;
    }

    return (
        <DialogPage buttons={buttons} decorations={decorations}>
            <DialogTitle>
                <MultilineText text={title} />
            </DialogTitle>
            {text ? (
                <DialogText>
                    <MultilineText text={text} />
                </DialogText>
            ) : null}
        </DialogPage>
    );
};
