import styled, { css } from 'styled-components';
import { transition } from 'style/const/transition';

interface GradientFadeProps {
    sizePx: number;
    kind: 'top' | 'bottom' | 'left' | 'right';
    isVisible: boolean;
}

export const GradientFade = styled.div<GradientFadeProps>`
    ${({ kind, sizePx }) => css`
        mask-image: linear-gradient(to ${kind}, #000 calc(100% - ${sizePx}px), transparent 100%);
        mask-size: calc(100% + ${sizePx}px) calc(100% + ${sizePx}px);
    `}

    mask-position: ${({ kind, isVisible, sizePx }) => {
        switch (kind) {
            case 'top':
                return `0 ${-1 * Number(!isVisible) * sizePx}px`;
            case 'bottom':
                return `0 ${-1 * Number(isVisible) * sizePx}px`;
            case 'left':
                return `${-1 * Number(!isVisible) * sizePx}px 0`;
            case 'right':
                return `${-1 * Number(isVisible) * sizePx}px 0`;
            default:
                return '0 0';
        }
    }};

    mask-repeat: no-repeat;
    transition: mask-position ${transition.default}, -webkit-mask-position ${transition.default};
`;
