import React from 'react';
import styled, { css } from 'styled-components';

import { ReactComponent as ShadowSvg } from './Pin.assets/shadow.svg';
import { ReactComponent as DeadFaceSvg } from './Pin.assets/dead-face.svg';
import { ReactComponent as HappyFaceSvg } from './Pin.assets/happy-face.svg';

const badgeHeightPx = 84;

export const DeadFaceIcon = styled(DeadFaceSvg)`
    display: block;
    height: 72px;
    width: 72px;
`;

export const HappyFaceIcon = styled(HappyFaceSvg)`
    display: block;
    height: 72px;
    width: 72px;
`;

interface StyledBodyProps {
    isBroken?: boolean;
}
const StyledBody = styled.div<StyledBodyProps>`
    position: relative;

    height: ${badgeHeightPx}px;
    min-width: ${badgeHeightPx}px;
    padding: 6px;
    border-radius: ${badgeHeightPx / 2}px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;

    text-align: center;

    ${({ isBroken }) =>
        isBroken
            ? css`
                  transform: rotate(-50deg);
                  left: -40px;
                  bottom: -20px;
              `
            : ''}
`;

const kernelWidthPx = 4;
const kernelBorderRadiusPx = kernelWidthPx / 2;

interface StyledKernelTopProps {
    isBroken?: boolean;
}
const StyledKernelTop = styled.div<StyledKernelTopProps>`
    position: relative;
    display: flex;

    height: 18px;
    width: ${kernelWidthPx}px;

    border-top-left-radius: ${kernelBorderRadiusPx}px;
    border-top-right-radius: ${kernelBorderRadiusPx}px;
    border-bottom-left-radius: ${kernelBorderRadiusPx}px;
    border-bottom-right-radius: ${kernelBorderRadiusPx}px;

    margin-bottom: -4px;
    margin-top: -2px;

    ${({ isBroken }) =>
        isBroken
            ? css`
                  transform: rotate(-50deg);
                  left: -6px;
                  bottom: -2px;
              `
            : ''}
`;

const StyledKernelBottom = styled.div`
    height: 26px;
    position: relative;
    width: ${kernelWidthPx}px;

    border-top-left-radius: ${kernelBorderRadiusPx}px;
    border-top-right-radius: ${kernelBorderRadiusPx}px;
    border-bottom-left-radius: ${kernelBorderRadiusPx}px;
    border-bottom-right-radius: ${kernelBorderRadiusPx}px;
`;

const StyledShadow = styled(ShadowSvg)`
    display: block;
    margin-top: -8px;

    width: 86px;
    height: 18px;
`;

const StyledPin = styled.div<PinInterface>`
    display: flex;
    flex-direction: column;
    align-items: center;

    ${StyledBody}, ${StyledKernelBottom}, ${StyledKernelTop} {
        background-color: ${({ pinColor }) => pinColor};
    }
`;

interface PinInterface {
    pinColor: string;
    isBroken?: boolean;
}
export const Pin: React.FC<PinInterface> = ({ pinColor, isBroken, children }) => {
    return (
        <StyledPin pinColor={pinColor}>
            <StyledBody isBroken={isBroken}>{children}</StyledBody>
            <StyledKernelTop isBroken={isBroken} />
            <StyledKernelBottom />
            <StyledShadow />
        </StyledPin>
    );
};
