import * as Sentry from '@sentry/react';
import destination from '@turf/destination';
import { memoize, memoizeResolvedPromise } from 'tools/memoize';

import { getSuggests } from './api/suggest';
import { DGLocale, isErrorResponse } from './api/common';

type Point = [string, string];

interface SearchBox {
    viewpoint1: Point;
    viewpoint2: Point;
}

const memoizedGetSuggests = memoizeResolvedPromise(getSuggests);

const primarySearchRadiusKm = 1;

const getPrimarySearchParams = memoize(
    (longitude: string, latitude: string): SearchBox => {
        const userLocation: [number, number] = [parseFloat(longitude), parseFloat(latitude)];

        const points = [0, 90, 180, 270].map(
            (direction) =>
                destination(userLocation, primarySearchRadiusKm, direction, { units: 'kilometers' }).geometry
                    ?.coordinates ?? userLocation,
        );

        const topLeft: Point = [
            Math.min(...points.map((point) => point[0])).toFixed(6),
            Math.max(...points.map((point) => point[1])).toFixed(6),
        ];

        const bottomRight: Point = [
            Math.max(...points.map((point) => point[0])).toFixed(6),
            Math.min(...points.map((point) => point[1])).toFixed(6),
        ];

        return {
            viewpoint1: topLeft,
            viewpoint2: bottomRight,
        };
    },
);

export interface GetSuggestsListParams {
    q: string;
    locale: DGLocale;
    longitude: string;
    latitude: string;
}

const defaultSuggestOptions = {
    fields: ['items.address', 'items.point'],
    suggestType: 'address',
    type: ['building'],
};

const getSuggestListBySearchParams = async (
    { q, locale, longitude, latitude }: GetSuggestsListParams,
    searchParams: SearchBox,
) =>
    memoizedGetSuggests({
        ...defaultSuggestOptions,
        q: q.toLowerCase(),
        locale,
        location: [longitude, latitude],
        ...searchParams,
    })
        .then((response) => {
            if (isErrorResponse(response)) {
                if (response.meta.code === 404) {
                    // не выкидываем ошибку, если ничего не нашли
                    return [];
                }

                throw new Error(`Error: 2GIS Suggests List: ${response.meta.error.message}`);
            }

            return response.result.items;
        })
        .catch((error) => {
            if (process.env.REACT_APP_ENVIRONMENT === 'production') {
                Sentry.captureException(error);
            } else {
                // eslint-disable-next-line no-console
                console.error(error);
            }

            return [];
        });

export const getSuggestsList = async (params: GetSuggestsListParams) => {
    const { q, longitude, latitude } = params;

    if (q.length === 0) {
        return [];
    }

    const searchParams = getPrimarySearchParams(longitude, latitude);

    const result = await getSuggestListBySearchParams(params, searchParams);

    if (!result.length) {
        return [];
    }

    return result;
};
