import { LocationPage } from 'components/LocationPage/LocationPage';
import { Pin, DeadFaceIcon, HappyFaceIcon } from 'components/Pin/Pin';
import { BodyText1 } from 'components/Text/Body';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';
import React from 'react';
import { goBack } from 'connected-react-router';
import { TwoGisSuggestFetchingStatus } from 'store/reducers/data/twoGisSuggestsData';
import { addressInputDeclineThunk, addressSubmitThunk } from 'store/thunks/native';
import styled from 'styled-components';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { HeadlineText1 } from 'components/Text/Headline';
import { Button, ButtonViews } from 'components/Button/Button';
import { color } from 'style/const/color';

const StyledTitle = styled(HeadlineText1)`
    margin-top: 48px;
    color: ${color.text.primary};
    text-align: center;
`;

const StyledSubTitle = styled(BodyText1)`
    margin-top: 12px;
    color: ${color.text.primary};
    text-align: center;
`;

const StyledContainer = styled.div`
    position: relative;
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    height: 100%;
`;

const StyledButtonContainer = styled.div`
    margin-top: 64px;
    display: flex;
    justify-content: center;
`;

const StyledButtonDivider = styled.div`
    width: 32px;
`;

export const AddressSimpleQuestion: React.FC = () => {
    const dispatch = useDispatch();
    const suggest = useSelector(({ twoGisSuggestsData: { mostRepresentationSuggest } }) => mostRepresentationSuggest);
    const suggestsFetchingStatus = useSelector(({ twoGisSuggestsData }) => twoGisSuggestsData.fetchingStatus);
    const inputValue = useSelector(({ addressSimplePage }) => addressSimplePage.inputValue);
    const isAddressSubmitting = useSelector(({ addressSimplePage }) => addressSimplePage.isAddressSubmitting);
    const handleAcceptButtonClick = useDebouncedOnceFn(
        () => {
            dispatch(addressSubmitThunk());
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleDeclineButtonClick = useDebouncedOnceFn(
        () => {
            dispatch(addressInputDeclineThunk());
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleRepeatClick = useDebouncedOnceFn(
        () => {
            dispatch(goBack());
        },
        debounceTimeoutMs,
        [dispatch],
    );

    useAddressSimpleUpdateSuggests();

    // саджесты еще не приехали, показываем пустой экран, потому здесь будет скелетон
    if (suggestsFetchingStatus === TwoGisSuggestFetchingStatus.PENDING) {
        return <LocationPage />;
    }

    if (!suggest) {
        return (
            <LocationPage>
                <StyledContainer>
                    <Pin isBroken pinColor={color.button.disabled}>
                        <DeadFaceIcon />
                    </Pin>
                    <StyledTitle>{inputValue}</StyledTitle>
                    <StyledSubTitle>Этот адрес не найден, попробуйте еще раз</StyledSubTitle>
                    <StyledButtonContainer>
                        <Button shouldFocusOnMount view={ButtonViews.secondary} onClick={handleRepeatClick}>
                            Ввести еще раз
                        </Button>
                    </StyledButtonContainer>
                </StyledContainer>
            </LocationPage>
        );
    }

    const addressName = buildAddressString(suggest);

    return (
        <LocationPage>
            <StyledContainer>
                <Pin pinColor={color.button.critical}>
                    <HappyFaceIcon />
                </Pin>
                <StyledTitle>{addressName}</StyledTitle>
                <StyledSubTitle>Это ваш адрес?</StyledSubTitle>
                <StyledButtonContainer>
                    <Button
                        shouldFocusOnMount
                        shouldLockAfterClick
                        view={ButtonViews.primary}
                        onClick={handleAcceptButtonClick}
                    >
                        Да, все верно
                    </Button>
                    <StyledButtonDivider />
                    <Button
                        view={ButtonViews.secondary}
                        onClick={handleDeclineButtonClick}
                        disabled={isAddressSubmitting}
                    >
                        Нет, выбрать другой
                    </Button>
                </StyledButtonContainer>
            </StyledContainer>
        </LocationPage>
    );
};
