import React, { useMemo } from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { ParagraphText2 } from 'components/Text/Paragraph@touch';
import { HeadlineText2 } from 'components/Text/Headline@touch';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout@touch';

import figureIcon1 from './Success.assets/figure-1.svg';
import figureIcon2 from './Success.assets/figure-2.svg';
import figureIcon3 from './Success.assets/figure-3.svg';
import figureIcon4 from './Success.assets/figure-4.svg';
import figureIcon5 from './Success.assets/figure-5.svg';
import figureIcon6 from './Success.assets/figure-6.svg';
import figureIcon7 from './Success.assets/figure-7.svg';
import figureIcon8 from './Success.assets/figure-8.svg';
import figureIcon9 from './Success.assets/figure-9.svg';

const SuccessTouchContainer = styled.div`
    position: relative;

    height: 100vh;

    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
`;

const SuccessTouchOrderInfo = styled.div`
    text-align: center;
    padding: 0 30px;

    position: absolute;
    top: 50%;
    transform: translateY(-50%);
    margin-top: -${layout.headerHeightPx}px;
`;

const SuccessTouchOrderNumber = styled(ParagraphText2)`
    color: ${color.text.secondary};
`;

const SuccessTouchOrderTime = styled(HeadlineText2)`
    margin-top: 10px;

    color: ${color.text.primary};
`;

interface FigureTouchProps {
    params: [string, number, number, number];
}

const SuccessTouchFigure = styled.div<FigureTouchProps>`
    position: absolute;

    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;

    ${({ params: [bgUrl, topPx, leftPx, sizePx] }) => css`
        top: ${topPx}%;
        left: ${leftPx}%;

        height: ${sizePx}px;
        width: ${sizePx}px;

        background-image: url('${bgUrl}');
    `}
`;

const figureList: FigureTouchProps['params'][] = [
    [figureIcon1, 6.1, 13.9, 30],
    [figureIcon2, 27.3, 83.7, 36],
    [figureIcon3, 0.6, 78, 24],
    [figureIcon4, 70.6, 74, 32],
    [figureIcon5, 51.4, 62.2, 31],
    [figureIcon6, 20.6, 51.3, 28],
    [figureIcon7, 15.6, 39.7, 18],
    [figureIcon8, 53.8, 46.5, 20],
    [figureIcon9, 63.8, 18, 36],
    [figureIcon4, 44.3, 11.3, 17],
];

export const SuccessTouch: React.FC = () => {
    const deliveryTime = useSelector(({ delivery }) => delivery.deliveryTime);
    const { orderId } = useSelector(({ cart }) => cart);
    const touchFigureList = useMemo(
        () => figureList.map((params, index) => <SuccessTouchFigure key={index} params={params} />),
        [],
    );

    return (
        <SuccessTouchContainer>
            <SuccessTouchOrderInfo>
                <SuccessTouchOrderNumber>Заказ #{orderId} оформлен</SuccessTouchOrderNumber>
                <SuccessTouchOrderTime>
                    Спасибо за заказ!
                    <br />
                    Будем у вас через {deliveryTime} минут.
                </SuccessTouchOrderTime>
            </SuccessTouchOrderInfo>
            {touchFigureList}
        </SuccessTouchContainer>
    );
};
