import { AppState } from 'store/constants';
import { splitByThousands } from 'tools/split-by-thousands';

import { formatPrice } from './catalogPage';

export const prepareTotalPrice = ({ cart: { totalPrice } }: AppState) => splitByThousands(formatPrice(totalPrice));

export const prepareToMinimumPrice = ({ cart: { totalPrice }, delivery: { minimalPrice } }: AppState) =>
    Math.max(0, Math.ceil((minimalPrice - totalPrice) / 100));

export const getFormattedPersonalAddress = ({ userData: { apartment, entrance, floor } }: AppState) =>
    [
        apartment && `квартира ${apartment}`, //
        entrance && `подъезд ${entrance}`,
        floor && `этаж ${floor}`,
    ]
        .filter(Boolean)
        .join(', ');

export const prepareHasRequiredData = ({ userData: { phoneNumber, address } }: AppState) =>
    Boolean(address && phoneNumber);

export const getWrapperYShift = (index: number, count: number, itemHeight: number) => {
    if (index === -1) {
        return 0;
    }

    // так как индекс начинается с 0, то расчеты бы имели смысл начиная
    // со второго элемента (индекс = 1), но нам нужно начинать скролл с
    // 3-го элемента, поэтому сдвигаем значение на 1
    let relatedIndex = Math.max(0, index - 1);

    // ограничиваем максимальное значение индекса
    relatedIndex = Math.min(Math.max(0, count - 4), relatedIndex);

    // домножаем отступ на коэффициент высоты айтема
    return relatedIndex * itemHeight;
};

export const prepareTopGradientVisible = (wrapperYShift: number) => wrapperYShift !== 0;

export const prepareBottomGradientVisible = (wrapperYShift: number, count: number, itemHeight: number) =>
    count > 4 && wrapperYShift !== (count - 4) * itemHeight;
