import React from 'react';

export function useFocusOnMount<T>(forwardRef?: React.RefObject<T>) {
    const ref = React.useRef<T>(null);

    const onMountRef = forwardRef || ref;

    React.useEffect(() => {
        if (onMountRef.current instanceof HTMLElement) {
            onMountRef.current.focus();
        }
    }, [onMountRef]);

    return onMountRef;
}
