import { AppReducer, AppState, initialState } from 'store/constants';
import { hasAddressComponents } from 'tools/twoGis/api/suggest';

export enum TwoGisSuggestFetchingStatus {
    INITIAL = 'INITIAL',
    PENDING = 'PENDING',
    FINISHED = 'FINISHED',
}

export const twoGisSuggestsData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'FINISH_FETCHING_TWO_GIS_SUGGESTS': {
            const [firstSuggest] = action.payload;

            state.twoGisSuggestsData.fetchingStatus = TwoGisSuggestFetchingStatus.FINISHED;
            state.twoGisSuggestsData.itemsMap = action.payload
                .filter((suggestItem) => hasAddressComponents(suggestItem))
                .reduce<AppState['twoGisSuggestsData']['itemsMap']>((result, item) => {
                    result[item.id] = item;

                    return result;
                }, {});

            state.twoGisSuggestsData.mostRepresentationSuggest = firstSuggest;

            break;
        }

        case 'ADDRESS_INPUT':
        case 'ADDRESS_INPUT_ADD':
        case 'address/CHANGE_INPUT_VALUE': {
            state.twoGisSuggestsData = { ...initialState.twoGisSuggestsData };

            break;
        }

        case 'START_FETCHING_TWO_GIS_SUGGESTS': {
            state.twoGisSuggestsData.fetchingStatus = TwoGisSuggestFetchingStatus.PENDING;

            break;
        }

        default: {
            break;
        }
    }
};
