import { ButtonViews } from 'components/Button/Button';
import { CartListItemViewProps } from 'pages/Cart/ListItem/CartListItem';
import {
    canAddMore,
    getCountById,
    getDisabled,
    getOldPriceById,
    preparePriceById,
    getQuantityLeftById,
    formatSale,
    wantMoreThanHave,
} from 'store/reducers/helpers/catalogPage';
import {
    getFormattedPersonalAddress,
    prepareHasRequiredData,
    prepareToMinimumPrice,
    prepareTotalPrice,
    getWrapperYShift,
    prepareTopGradientVisible,
    prepareBottomGradientVisible,
} from 'store/reducers/helpers/cartPage';
import { selectProductById, selectCartItemById, selectProductDiffById } from 'store/selectors';
import { AppReducer, AppState } from 'store/constants';
import { formatPhone } from 'tools/phoneNumber';

const prepareLabel = (state: AppState, itemId: string): CartListItemViewProps['label'] => {
    const quantityLeft = getQuantityLeftById(state, itemId);
    const productItem = selectProductById(state, itemId);
    const productItemDiff = selectProductDiffById(state, itemId);
    const cartItem = selectCartItemById(state, itemId);
    const isWantMoreThanHave = wantMoreThanHave(state, itemId);

    // раскупили
    if (quantityLeft === 0) {
        return { type: 'sold-out' };
    }

    // есть меньше чем добавили
    if (isWantMoreThanHave) {
        return {
            type: 'you-want',
            content: String(cartItem.count),
        };
    }

    // изменение цены
    if (productItemDiff && typeof productItemDiff.price !== 'undefined') {
        // подорожание
        if (productItemDiff.price > productItem.price) {
            return { type: 'price-changed' };
        }

        // скидка
        if (productItemDiff.price < productItem.price) {
            const content = formatSale(productItem.price, productItemDiff.price);
            return { type: 'sale', content };
        }
    }

    return undefined;
};

const prepareItemsMap = (state: AppState) => {
    const { cartPage } = state;

    return cartPage.order.reduce<AppState['cartPage']['itemsMap']>((result, itemId) => {
        const cartItem = selectCartItemById(state, itemId);
        const { imageUrl, name } = selectProductById(state, itemId);

        if (cartItem) {
            result[itemId] = {
                imageUrl,
                name,
                price: preparePriceById(state, itemId),
                oldPrice: getOldPriceById(state, itemId),
                count: getCountById(state, itemId),
                isWantMoreThanHave: wantMoreThanHave(state, itemId),
                disabled: getDisabled(state, itemId),
                label: prepareLabel(state, itemId),
                isMoreButtonDisabled: !canAddMore(state, itemId),
            };
        }

        return result;
    }, {});
};

const itemHeight = 152;

const calculateMakePurchaseDisabled = (state: AppState) => {
    const {
        cart: { totalPrice },
        delivery: { minimalPrice },
    } = state;

    return totalPrice < minimalPrice;
};

const calculateMakePurchaseButtonView = (state: AppState): AppState['cartPage']['makePurchaseButtonView'] => {
    return state.cartPage.isMakePurchaseButtonDisabled ? ButtonViews.secondary : ButtonViews.primary;
};

const getFormattedPhoneNumber = ({ userData }: AppState) => userData.phoneNumber && formatPhone(userData.phoneNumber);

export const cartPageView: AppReducer = (state, action) => {
    const { cart, delivery, cartPage } = state;

    switch (action.type) {
        case 'cart/SELECT_ITEM': {
            cartPage.selectedItemIndex = cartPage.order.indexOf(action.payload);

            break;
        }

        default:
            break;
    }

    cartPage.order = cart.order;
    cartPage.deliveryTime = delivery.deliveryTime;
    cartPage.itemsMap = prepareItemsMap(state);
    cartPage.totalPrice = prepareTotalPrice(state);
    cartPage.toMinimumPrice = prepareToMinimumPrice(state);
    cartPage.wrapperYShift = getWrapperYShift(
        state.cartPage.selectedItemIndex,
        state.cartPage.order.length,
        itemHeight,
    );
    cartPage.isMakePurchaseButtonDisabled = calculateMakePurchaseDisabled(state);

    cartPage.makePurchaseButtonView = calculateMakePurchaseButtonView(state);
    cartPage.topGradientVisible = prepareTopGradientVisible(cartPage.wrapperYShift);
    cartPage.bottomGradientVisible = prepareBottomGradientVisible(
        cartPage.wrapperYShift,
        cartPage.order.length,
        itemHeight,
    );
    cartPage.hasRequiredData = prepareHasRequiredData(state);
    cartPage.phoneNumber = getFormattedPhoneNumber(state);
    cartPage.personalAddress = getFormattedPersonalAddress(state);
};
