import { goBack } from 'connected-react-router';
import { Button, ButtonViews } from 'components/Button/Button';
import { GradientFade } from 'components/GradientFade/GradientFade';
import { HeadlineText1 } from 'components/Text/Headline';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { useAddressSimpleUpdateSuggests } from 'pages/AddressSimple/AddressSimple.hooks';
import React from 'react';
import { color } from 'style/const/color';
import styled from 'styled-components';
import Swiper from 'swiper';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';

import { AddressSimpleSuggestListItem } from './SuggestItem/AddressSimpleSuggestItem';

const slideHeight = 112;
const spaceBetween = 24;
const slideWithMarginHeight = slideHeight + spaceBetween;
const paddingForFocus = 8;
const itemsInViewport = 4;

const StyledSwiperContainer = styled.div`
    margin: ${80 - paddingForFocus}px auto 0;
    width: ${900 + paddingForFocus * 2}px;
    height: ${slideWithMarginHeight * itemsInViewport - spaceBetween + paddingForFocus * 2 + slideHeight / 2}px;
    overflow-y: hidden;
`;

const StyledSwiperWrapper = styled.div``;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

const StyledSwiperSlide = styled.div`
    height: ${slideHeight}px;
    padding: 0 8px;
`;

const StyledTitle = styled(HeadlineText1)`
    margin-top: 48px;
    color: ${color.text.primary};
    text-align: center;
`;

const StyledButton = styled(Button)`
    width: 100%;
    padding: 36px 0;
`;

export const AddressSimple: React.FC = () => {
    const dispatch = useDispatch();
    const isAddressSubmitting = useSelector(({ addressSimplePage }) => addressSimplePage.isAddressSubmitting);
    const suggestsListIndex = useSelector(({ addressSimplePage }) => addressSimplePage.suggestsListIndex);
    const suggestIds = useSelector(({ addressSimplePage }) => addressSimplePage.suggests);
    const swiperContainerRef = React.useRef<HTMLDivElement>(null);
    const swiperRef = React.useRef<Swiper | null>(null);

    useAddressSimpleUpdateSuggests();

    React.useLayoutEffect(() => {
        if (swiperContainerRef.current) {
            swiperRef.current =
                swiperRef.current ||
                new Swiper(swiperContainerRef.current, {
                    direction: 'vertical',
                    slidesPerView: 'auto',
                    speed: 400,
                    spaceBetween,
                    initialSlide: 0,
                    slidesOffsetBefore: paddingForFocus,
                    slidesOffsetAfter: paddingForFocus + 52,
                });
        }
    }, []);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.updateSize();
            swiperRef.current.updateSlides();
            swiperRef.current.updateProgress();
            swiperRef.current.updateSlidesClasses();
        }
    }, [suggestIds]);

    React.useLayoutEffect(() => {
        if (swiperRef.current) {
            swiperRef.current.slideTo(suggestsListIndex - 1, 400);
        }
    }, [suggestsListIndex]);

    const handleCantFindAddressClick = useDebouncedOnceFn(
        () => {
            dispatch(goBack());
        },
        debounceTimeoutMs,
        [dispatch],
    );

    const suggestItems = React.useMemo(() => {
        return suggestIds
            .map((itemId, index) => (
                <StyledSwiperSlide className="swiper-slide" key={itemId}>
                    <AddressSimpleSuggestListItem shouldFocusOnMount={index === 0} id={itemId} />
                </StyledSwiperSlide>
            ))
            .concat([
                <StyledSwiperSlide className="swiper-slide" key="button">
                    <StyledButton
                        view={ButtonViews.primary}
                        onClick={handleCantFindAddressClick}
                        disabled={isAddressSubmitting}
                    >
                        Моего адреса нет в списке
                    </StyledButton>
                </StyledSwiperSlide>,
            ]);
    }, [suggestIds, handleCantFindAddressClick, isAddressSubmitting]);

    const isTopGradientVisible = suggestsListIndex > 1;
    const isBottomGradientVisible =
        // prettier-ignore
        (suggestsListIndex === 0 && suggestItems.length > 1) ||
        // -2 потому что все элементы уже видны, когда мы доходим до этого индекса
        // пора убирать градиент снизу
        suggestsListIndex < suggestItems.length - 1 - 2;

    return (
        <>
            <StyledTitle>Выберите адрес</StyledTitle>
            <GradientFade sizePx={150} isVisible={isTopGradientVisible} kind="top">
                <GradientFade sizePx={250} isVisible={isBottomGradientVisible} kind="bottom">
                    <StyledSwiperContainer
                        tabIndex={-1}
                        className="swiper-container"
                        ref={swiperContainerRef}
                        onScroll={preventScroll}
                    >
                        <StyledSwiperWrapper className="swiper-wrapper">{suggestItems}</StyledSwiperWrapper>
                    </StyledSwiperContainer>
                </GradientFade>
            </GradientFade>
        </>
    );
};
