export enum OrderStatus {
    INITIAL = 'INITIAL',
    INVOICE_CREATED = 'INVOICE_CREATED',
    FUNDS_RESERVED = 'FUNDS_RESERVED',
    CONFIRMED_BY_PARTNER = 'CONFIRMED_BY_PARTNER',
    DELIVERING = 'DELIVERING',
    DELIVERED = 'DELIVERED',
    CANCEL_BY_PARTNER = 'CANCEL_BY_PARTNER',
    CANCEL_BY_CUSTOMER = 'CANCEL_BY_CUSTOMER',
    CANCEL_BY_PAYMENT = 'CANCEL_BY_PAYMENT',
    PAID = 'PAID',
    CANCELLED = 'CANCELLED',
    ERROR = 'ERROR',
}

export enum OrderStatusCategory {
    PENDING = 'PENDING',
    DELIVERING = 'DELIVERING',
    SUCCESS = 'SUCCESS',
    FAILED = 'FAILED',
}

export const mapOrderStatusToCategory: Record<OrderStatus, OrderStatusCategory> = {
    [OrderStatus.INITIAL]: OrderStatusCategory.PENDING,
    [OrderStatus.INVOICE_CREATED]: OrderStatusCategory.PENDING,
    [OrderStatus.FUNDS_RESERVED]: OrderStatusCategory.PENDING,
    [OrderStatus.CONFIRMED_BY_PARTNER]: OrderStatusCategory.PENDING,
    [OrderStatus.DELIVERING]: OrderStatusCategory.DELIVERING,
    [OrderStatus.DELIVERED]: OrderStatusCategory.SUCCESS,
    [OrderStatus.CANCEL_BY_PARTNER]: OrderStatusCategory.FAILED,
    [OrderStatus.CANCEL_BY_CUSTOMER]: OrderStatusCategory.FAILED,
    [OrderStatus.CANCEL_BY_PAYMENT]: OrderStatusCategory.FAILED,
    [OrderStatus.PAID]: OrderStatusCategory.SUCCESS,
    [OrderStatus.CANCELLED]: OrderStatusCategory.FAILED,
    [OrderStatus.ERROR]: OrderStatusCategory.FAILED,
};

export enum Currency {
    RUB = 'RUB',
}

export interface Price {
    value: string;
    ccy: Currency;
}

export interface DiscountedPrice extends Price {
    discountValue: string;
}

export interface ParsedPrice {
    value: number;
    currency: Currency;
}

export interface ParsedDiscountedPrice extends ParsedPrice {
    discountValue: number;
}

export interface LastOrderPosition {
    positionId: string;
    productId: string;
    originalPrice: DiscountedPrice;
    originalTotalPrice: DiscountedPrice;
    originalQuantity: string;
    formedPrice: DiscountedPrice;
    formedQuantity: string;
    formedTotalPrice: DiscountedPrice;
    name: string;
    images_urls: string[];
}

export interface LastOrderInfo {
    orderId: string;
    partner: string;
    merchantId: string;
    externalId: string;
    invoiceId: string;
    orderStatus: OrderStatus;
    changedOnFormed: string;
    expectedDateTime: string;
    comment: string;
    customer: {
        sub: string;
        name: string;
        phone: string;
        email: string;
    };
    address: {
        location: {
            latitude: string;
            longitude: string;
        };
        city: string;
        street: string;
        house: string;
        apartment: string;
        entrance: string;
        floor: string;
        door_code: string;
    };
    positions: LastOrderPosition[];
    deliveryPrice: DiscountedPrice;
    orderDiscount: Price;
    originalTotalPrice: DiscountedPrice;
    originalOrderPrice: Price;
    formedTotalPrice: DiscountedPrice;
    formedOrderPrice: Price;
    errorCode: string;
}

export interface OrderStateProductItem {
    positionId: string;
    productId: string;
    originalPrice: ParsedDiscountedPrice;
    originalTotalPrice: ParsedDiscountedPrice;
    originalQuantity: number;
    formedPrice: ParsedDiscountedPrice;
    formedQuantity: number;
    formedTotalPrice: ParsedDiscountedPrice;
    name: string;
    imageUrl: string;
}
