import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import React from 'react';
import { addressShowKeyboardHintAction } from 'store/actions/addressSimplePage';
import { ROUTES } from 'store/constants/router';
import {
    GuidePageHint,
    GuidePagePlasmaIcon,
    GuidePageRoot,
    GuidePageSource,
    GuidePageTitle,
} from 'components/GuidePage/GuidePage';

export const AddressSimpleGuide: React.FC = () => {
    const isKeyboardHintVisible = useSelector(({ addressSimplePage }) => addressSimplePage.isKeyboardHintVisible);
    const dispatch = useDispatch();
    const handleOkClick = useDebouncedOnceFn(
        () => {
            dispatch(push(ROUTES.ADDRESS_SIMPLE_INPUT));
        },
        debounceTimeoutMs,
        [dispatch],
    );

    React.useEffect(() => {
        return () => {
            if (!isKeyboardHintVisible) {
                dispatch(addressShowKeyboardHintAction());
            }
        };
    }, [dispatch, isKeyboardHintVisible]);

    return (
        <GuidePageRoot>
            <GuidePagePlasmaIcon />
            <GuidePageTitle>Нажмите кнопку на пульте и назовите</GuidePageTitle>
            <GuidePageSource>улицу и дом</GuidePageSource>
            {isKeyboardHintVisible ? <GuidePageHint onOkClick={handleOkClick} /> : null}
        </GuidePageRoot>
    );
};
