import React from 'react';
import { scenarioResponseTimeout } from 'store/constants';
import styled, { css } from 'styled-components';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { color } from 'style/const/color';
import { focusBorderMixin } from 'style/mixin/focus';
import { font } from 'style/const/font';
import { ButtonSpinner } from 'components/ButtonSpinner/ButtonSpinner';
import { transition } from 'style/const/transition';

interface StyledContentProps {
    isVisible: boolean;
    textAlign: ButtonTextAlign;
}
const StyledContent = styled.div<StyledContentProps>`
    opacity: ${({ isVisible }) => Number(isVisible)};
    transition: opacity ${transition.default};
    width: 100%;
    display: block;
    text-align: ${({ textAlign }) => textAlign};
`;

export enum ButtonViews {
    primary = 'primary',
    secondary = 'secondary',
}

export enum ButtonSize {
    l = 'l',
    m = 'm',
    s = 's',
}

export enum ButtonTextAlign {
    left = 'left',
    center = 'center',
    right = 'right',
}

interface ButtonParams {
    borderRadiusPx: number;
    paddingXPx: number;
    paddingYPx: number;
    fontSizePx: number;
    lineHeightPx: number;
}

const mapSizeToButtonParams: Record<ButtonSize, ButtonParams> = {
    [ButtonSize.l]: {
        borderRadiusPx: 32,
        paddingXPx: 56,
        paddingYPx: 32,
        fontSizePx: 32,
        lineHeightPx: 40,
    },
    [ButtonSize.m]: {
        borderRadiusPx: 24,
        paddingXPx: 48,
        paddingYPx: 24,
        fontSizePx: 32,
        lineHeightPx: 40,
    },
    [ButtonSize.s]: {
        borderRadiusPx: 24,
        paddingXPx: 28,
        paddingYPx: 16,
        fontSizePx: 28,
        lineHeightPx: 32,
    },
};

const buttonViewBackgroundMap: Record<ButtonViews, string> = {
    primary: color.button.primary,
    secondary: color.button.secondary1,
};

const StyledButtonSpinner = styled(ButtonSpinner)`
    position: absolute;

    transition: opacity ${transition.default};
`;

interface StyledButtonProps {
    view: ButtonViews;
    size: ButtonSize;
    isLocked: boolean;
}
const StyledButton = styled.div<StyledButtonProps>`
    position: relative;

    ${({ size }) => {
        const { borderRadiusPx, paddingXPx, paddingYPx, fontSizePx, lineHeightPx } = mapSizeToButtonParams[size];

        return css`
            padding: ${paddingYPx}px ${paddingXPx}px;
            font-size: ${fontSizePx}px;
            line-height: ${lineHeightPx}px;
            border-radius: ${borderRadiusPx}px;
        `;
    }}

    border: none;

    display: flex;
    align-items: center;
    justify-content: center;

    transition: background-color ${transition.default};

    background-color: ${({ view }) => buttonViewBackgroundMap[view]};

    font-family: inherit;
    font-weight: ${font.weight.semibold};
    color: ${color.text.primary};

    ${({ size }) => focusBorderMixin({ borderRadius: mapSizeToButtonParams[size].borderRadiusPx })}

    &[disabled] {
        opacity: ${({ isLocked }) => (isLocked ? 1 : 0.5)};
    }
`;

const usePressedState = ({
    shouldLockAfterClick,
    lockTimeout = scenarioResponseTimeout,
    onClick,
}: Pick<ButtonProps, 'shouldLockAfterClick' | 'lockTimeout' | 'onClick'>) => {
    const [timeoutId, setTimeoutId] = React.useState(0);

    // если таймаут установлен - кнопка заблокирована
    const isLocked = Boolean(timeoutId);

    const handleClick = React.useCallback(
        (event: React.SyntheticEvent<HTMLButtonElement>) => {
            if (shouldLockAfterClick && !isLocked) {
                setTimeoutId(setTimeout(() => setTimeoutId(0), lockTimeout));
            }

            onClick?.(event);
        },
        [shouldLockAfterClick, lockTimeout, isLocked, onClick],
    );

    React.useEffect(() => {
        const id = timeoutId;
        return () => {
            if (id) {
                clearTimeout(id);
            }
        };
    }, [timeoutId]);

    return {
        isLocked,
        handleClick,
    };
};

export interface ButtonProps {
    className?: string;
    shouldFocusOnMount?: boolean;
    forwardRef?: React.RefObject<HTMLButtonElement>;
    view?: ButtonViews;
    disabled?: boolean;
    size?: ButtonSize;
    textAlign?: ButtonTextAlign;
    shouldLockAfterClick?: boolean;
    lockTimeout?: number;
    onClick?: (event: React.SyntheticEvent<HTMLButtonElement>) => void;
    onFocus?: (event: React.FocusEvent<HTMLButtonElement>) => void;
}
export const Button: React.FC<ButtonProps> = ({
    shouldFocusOnMount,
    forwardRef,
    shouldLockAfterClick,
    lockTimeout,
    onClick,
    view = ButtonViews.secondary,
    size = ButtonSize.m,
    textAlign = ButtonTextAlign.center,
    children,
    disabled,
    ...props
}) => {
    const mountFocusRef = useFocusOnMount<HTMLButtonElement>();
    const ref = forwardRef || (shouldFocusOnMount ? mountFocusRef : null);
    const { isLocked, handleClick } = usePressedState({
        shouldLockAfterClick,
        lockTimeout,
        onClick,
    });

    return (
        <StyledButton
            as="button"
            type="button"
            view={view}
            size={size}
            ref={ref}
            onClick={handleClick}
            isLocked={isLocked}
            disabled={disabled || isLocked}
            {...props}
        >
            <StyledButtonSpinner isVisible={isLocked} pointCount={3} phaseCount={3} phaseDurationMs={300} />
            <StyledContent isVisible={!isLocked} textAlign={textAlign}>
                {children}
            </StyledContent>
        </StyledButton>
    );
};
