import { selectCartItemById, selectProductById } from 'store/selectors';
import { AppReducer, AppState } from 'store/constants';
import { canAddMore, getCountById, getDisabled, preparePriceById } from 'store/reducers/helpers/catalogPage';
import {
    getFormattedPersonalAddress,
    prepareHasRequiredData,
    prepareToMinimumPrice,
    prepareTotalPrice,
} from 'store/reducers/helpers/cartPage';

const prepareItemsMap = (state: AppState) => {
    const { cartPageTouch } = state;

    return cartPageTouch.order.reduce<AppState['cartPageTouch']['itemsMap']>((result, itemId) => {
        const cartItem = selectCartItemById(state, itemId);
        const { imageUrl, name } = selectProductById(state, itemId);

        if (cartItem) {
            result[itemId] = {
                imageUrl,
                name,
                price: preparePriceById(state, itemId),
                count: getCountById(state, itemId),
                disabled: getDisabled(state, itemId),
                isMoreButtonDisabled: !canAddMore(state, itemId),
            };
        }

        return result;
    }, {});
};

export const cartPageTouchView: AppReducer = (state) => {
    const { cart, delivery, cartPageTouch } = state;

    cartPageTouch.order = cart.order;
    cartPageTouch.deliveryTime = delivery.deliveryTime;
    cartPageTouch.itemsMap = prepareItemsMap(state);
    cartPageTouch.totalPrice = prepareTotalPrice(state);
    cartPageTouch.toMinimumPrice = prepareToMinimumPrice(state);
    cartPageTouch.hasRequiredData = prepareHasRequiredData(state);
    cartPageTouch.personalAddress = getFormattedPersonalAddress(state);
};
