import { LocationPage } from 'components/LocationPage/LocationPage';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import React from 'react';
import styled from 'styled-components';
import { replace } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { ROUTES } from 'store/constants/router';
import { closeAppAction } from 'store/actions/app';
import { Pin } from 'components/Pin/Pin';
import { HeadlineText1 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { Button, ButtonViews } from 'components/Button/Button';
import { color } from 'style/const/color';

import faceIcon from './NoArea.assets/face.svg';

const faceIconSizePx = 36;

const StyledFaceIcon = styled.div`
    height: ${faceIconSizePx}px;
    width: ${faceIconSizePx}px;

    background-image: url(${faceIcon});
    background-repeat: no-repeat;
    background-position: center;
    background-size: contain;
`;

const StyledTitle = styled(HeadlineText1)`
    margin-top: 52px;

    color: ${color.text.primary};
`;

const StyledSubtitle = styled(BodyText1)`
    margin-top: 12px;

    color: ${color.text.primary};
`;

const StyledButtonContainer = styled.div`
    display: flex;
    justify-content: center;
`;

const StyledButton = styled(Button)`
    margin-top: 64px;
    margin-left: 40px;
`;

const StyledContainer = styled.div`
    display: flex;
    height: 100%;

    justify-content: center;
    align-items: center;
    flex-direction: column;
`;

export const NoArea: React.FC = () => {
    const dispatch = useDispatch();
    const address = useSelector(({ userModel }) => userModel.address);
    const city = useSelector(({ userModel }) => userModel.city);
    const handleChangeButtonClick = useDebouncedOnceFn(
        () => {
            dispatch(replace(ROUTES.ADDRESS_SIMPLE_GUIDE));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleCloseButtonClick = useDebouncedOnceFn(
        () => {
            dispatch(closeAppAction());
        },
        debounceTimeoutMs,
        [dispatch],
    );

    const addressString = [city, address].join(', ');

    return (
        <LocationPage>
            <StyledContainer>
                <Pin pinColor={color.button.disabled}>
                    <StyledFaceIcon />
                </Pin>
                <StyledTitle>{addressString}</StyledTitle>
                <StyledSubtitle>К сожалению, пока не доставляем по этому адресу</StyledSubtitle>
                <StyledButtonContainer>
                    <StyledButton view={ButtonViews.primary} shouldFocusOnMount onClick={handleChangeButtonClick}>
                        Ввести другой
                    </StyledButton>
                    <StyledButton onClick={handleCloseButtonClick} view={ButtonViews.secondary}>
                        Не сейчас
                    </StyledButton>
                </StyledButtonContainer>
            </StyledContainer>
        </LocationPage>
    );
};
