import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { closeAppAction } from 'store/actions/app';
import { ButtonViews } from 'components/Button/Button';
import {
    DialogPage,
    DialogButton,
    DialogTitle,
    FullscreenDecoration,
    DialogText,
} from 'components/DialogPage/DialogPage';
import { SplendidAnimation } from 'components/SplendidAnimation/SplendidAnimation';
import { MultilineText } from 'components/MultilineText/MultilineText';
import { layout } from 'style/const/layout';

import successAnimationData from './Success.assets/success.animation.json';

export const Success: React.FC = () => {
    const dispatch = useDispatch();
    const { deliveryTime } = useSelector(({ delivery }) => delivery);

    const buttons = React.useMemo(
        () => (
            <DialogButton
                shouldFocusOnMount
                view={ButtonViews.primary}
                onClick={() => {
                    dispatch(closeAppAction());
                }}
            >
                Очень жду
            </DialogButton>
        ),
        [dispatch],
    );

    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <SplendidAnimation
                    width={layout.widthPx}
                    height={layout.heightPx}
                    delayMs={300}
                    config={{
                        loop: false,
                        animationData: successAnimationData,
                    }}
                />
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPage buttons={buttons} decorations={decorations}>
            <DialogTitle>
                <MultilineText text={`Заказ принят. \nБудем у вас через \n${deliveryTime} минут.`} />
            </DialogTitle>
            <DialogText>
                <MultilineText text={'Чтобы узнать статус заказа, спросите \n«Какой статус заказа?»'} />
            </DialogText>
        </DialogPage>
    );
};
