import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { CartInfoPin, CartInfoContainer, CartPersonal, CartAddress } from 'components/Cart/CartInfo';

export const StatusDetailsInfo: React.FC = () => {
    const { address } = useSelector(({ userData }) => userData);
    const { phoneNumber, personalAddress } = useSelector(({ orderStatusDetailsPage }) => orderStatusDetailsPage);

    return (
        <CartInfoContainer>
            <CartInfoPin />
            <CartAddress>{address}</CartAddress>
            <CartPersonal address={personalAddress} phone={phoneNumber} />
        </CartInfoContainer>
    );
};
