import { AppReducer } from 'store/constants';
import { ProductItemDiff, StoreProductItemDiff } from 'store/constants/products';

const normalizeProductItemsDiffMap = (productsDiffMap: Record<string, ProductItemDiff>) =>
    Object.entries(productsDiffMap).reduce((result, [itemId, productItemDiff]) => {
        const { quantity_left: quantityLeftString, price: priceString } = productItemDiff;

        if (!quantityLeftString && !priceString) {
            return result;
        }

        result[itemId] = {};

        if (quantityLeftString) {
            result[itemId].quantityLeft = Number(quantityLeftString);
        }

        if (priceString) {
            result[itemId].price = Number(priceString);
        }

        return result;
    }, {} as Record<string, StoreProductItemDiff>);

export const productsDiffData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ITEMS_CHANGES': {
            state.productsDiffData.itemsMap = normalizeProductItemsDiffMap(action.payload.changes);

            break;
        }

        case 'APPLY_ITEMS_CHANGES': {
            state.productsDiffData.itemsMap = {};

            break;
        }

        default: {
            break;
        }
    }
};
