import React from 'react';
import styled from 'styled-components';
import { Header } from 'components/Header/Header';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';
import gradientBackground from 'assets/images/background-tv-sber.jpg';

const StyledContainer = styled.div`
    position: relative;

    width: 100%;
    height: 100%;

    overflow: hidden;

    background-color: ${color.background.default};
    background-image: url('${gradientBackground}');
    background-position: center;
    background-size: cover;
`;

const AppContent = styled.div`
    position: relative;
    z-index: 2;

    height: ${layout.contentHeightPx}px;
`;

const StyledHeader = styled(Header)`
    z-index: 3;
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const AppLayout: React.FC = ({ children }) => {
    return (
        <StyledContainer onScroll={preventScroll}>
            <StyledHeader />
            <AppContent>{children}</AppContent>
            {/* <MapBackground isVisible={isMapVisible} maskType={maskType} /> */}
        </StyledContainer>
    );
};
