import React from 'react';
import styled, { keyframes, css } from 'styled-components';
import { color } from 'style/const/color';
import { transition } from 'style/const/transition';

interface PointProps {
    phaseCount: number;
    phaseDurationMs: number;
    initialPhase: number;
}

const StyledPoint = styled.div<PointProps>`
    width: 16px;
    height: 16px;
    border-radius: 50%;
    margin: 0 8px;

    background: ${color.surface.solidWhite};

    animation-iteration-count: infinite;
    animation-timing-function: linear;

    ${({ initialPhase, phaseCount, phaseDurationMs }) => {
        const cycleLength = phaseCount * 2 - 1;
        const transitionCount = cycleLength - 1;

        const frames = Array(cycleLength)
            .fill(0)
            .map((_, index) => {
                const percent = (100 * index) / transitionCount;

                // точка колеблется между тремя состояниями, меняя направление:
                // такое поведение математически проще всего описать синусоидой
                // c указанием начальной фазы:
                //
                // 0 - 0 -> 1 -> 2 -> 1 -> 0 -> 1 -> 2 -> ...
                // 1 - 1 -> 2 -> 1 -> 0 -> 1 -> 2 -> 1 -> ...
                // 2 - 2 -> 1 -> 0 -> 1 -> 2 -> 1 -> 0 -> ...
                // 3 - 1 -> 0 -> 1 -> 2 -> 1 -> 0 -> 1 -> ...
                //
                // синусоида колеблется в интервале [-1;1]
                // чтобы значения были положительными, сдвигаем синусоиду на 1 вверх и уменьшаем амплитуду вдвое
                // получаем интервал [0;1], дальше домножаем на (число фаз - 1) и округляем

                const phaseIndex = Math.round(
                    (phaseCount - 1) * ((Math.sin((2 * Math.PI * (index + initialPhase)) / transitionCount) + 1) / 2),
                );

                const result = css`
                    ${`${percent}%`} {
                        opacity: ${((phaseIndex + 1) / phaseCount).toFixed(1)};
                    }
                `;

                return result;
            })
            .reduce((a, b) => css`${a}${b}`, css``);

        return css`
            animation-duration: ${phaseDurationMs * transitionCount}ms;
            animation-name: ${keyframes`${frames}`};
        `;
    }}
`;

interface ContainerProps {
    isVisible: boolean;
}
const StyledContainer = styled.div<ContainerProps>`
    display: flex;
    flex-direction: row;

    opacity: ${({ isVisible }) => (isVisible ? 1 : 0)};
    transition: opacity ${transition.default};

    ${StyledPoint} {
        animation-play-state: ${({ isVisible }) => (isVisible ? 'running' : 'paused')};
    }
`;

interface ButtonSpinnerProps {
    isVisible: boolean;
    pointCount: number;
    phaseCount: number;
    phaseDurationMs: number;
}
export const ButtonSpinner: React.FC<ButtonSpinnerProps> = ({ pointCount, phaseCount, phaseDurationMs, ...props }) => {
    const pointList = React.useMemo(
        () =>
            Array(pointCount)
                .fill(0)
                .map((_, index) => (
                    <StyledPoint
                        key={index}
                        phaseDurationMs={phaseDurationMs}
                        phaseCount={phaseCount}
                        initialPhase={pointCount - index - 1}
                    />
                )),
        [pointCount, phaseCount, phaseDurationMs],
    );

    return <StyledContainer {...props}>{pointList}</StyledContainer>;
};
