import React from 'react';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { statusDetailsSelectItemAction } from 'store/actions/statusDetailsPage';
import { LabelProps } from 'components/Label/Label';
import { CartListItemRoot, CartListItemThumb, CartListItemInfo } from 'components/Cart/CartListItem';

export interface CartListItemProps {
    id: string;
    index: number;
    canFocus?: boolean;
}

export interface StatusDetailsListItemViewProps {
    productId: string;
    imageUrl: string;
    name: string;
    disabled?: boolean;
    price: number;
    oldPrice?: number;
    count: number;
    label?: LabelProps;
}

export const StatusDetailsListItem: React.FC<CartListItemProps> = ({ id, canFocus }) => {
    const { imageUrl, name, price, oldPrice, label, count, disabled } = useSelector(
        ({ orderStatusDetailsPage: { itemsMap } }) => itemsMap[id],
    );
    const dispatch = useDispatch();

    const handleFocus = React.useCallback(() => dispatch(statusDetailsSelectItemAction(id)), [dispatch, id]);

    return (
        <CartListItemRoot tabIndex={canFocus ? 0 : undefined} disabled={disabled} onFocus={handleFocus}>
            <CartListItemThumb imageUrl={imageUrl} />
            <CartListItemInfo
                disabled={disabled}
                label={label}
                name={name}
                price={price}
                oldPrice={oldPrice}
                count={count}
            />
        </CartListItemRoot>
    );
};
