import popcornBox1 from './Promo.assets/popcorn-box_2.png';
import popcornBox3 from './Promo.assets/popcorn-box_3.png';
import popcornBox4 from './Promo.assets/popcorn-box_4.png';
import popcorn1 from './Promo.assets/popcorn-1.png';
import popcorn2 from './Promo.assets/popcorn-2.png';
import popcorn3 from './Promo.assets/popcorn-3.png';

const popcornBoxList: FlattenSimpleInterpolation[] = [
    css`
        width: 250px;
        height: 385px;
        background-image: url('${popcornBox1}');
    `,
    css`
        width: 250px;
        height: 280px;
        margin-top: 84px;
        background-image: url('${popcornBox2}');
    `,
    css`
        width: 250px;
        height: 280px;
        margin-top: 84px;
        background-image: url('${popcornBox3}');
    `,
    css`
        width: 250px;
        height: 380px;
        margin-top: 31px;
        background-image: url('${popcornBox4}');
    `,
];

const popcornList: FlattenSimpleInterpolation[] = [
    css`
        width: 54px;
        height: 54px;
        top: 41px;
        left: 260px;
        background-image: url(${popcorn2});
    `,
    css`
        width: 71px;
        height: 71px;
        top: 226px;
        left: 75px;
        background-image: url(${popcorn1});
    `,
    css`
        width: 76px;
        height: 76px;
        top: 271px;
        left: 287px;
        background-image: url(${popcorn1});
    `,
    css`
        width: 64px;
        height: 64px;
        top: 311px;
        left: 7px;
        background-image: url(${popcorn3});
    `,
    css`
        width: 74px;
        height: 74px;
        top: 530px;
        left: 274px;
        background-image: url(${popcorn3});
    `,
];

/* Slider */
const StyledSlider = styled(Slider)`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
`;

interface PopcornBoxProps {
    popcornStyle: FlattenSimpleInterpolation;
}

const PopcornBox = styled.div<PopcornBoxProps>`
    margin: 26px auto 0;

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    transition: opacity 1s ${transition.timingFn.default};

    ${({ popcornStyle }) => popcornStyle}
`;

/* Text container */
const TextContainer = styled.div`
    position: relative;
    text-align: center;
    padding: 253px 24px 0;
    user-select: none;
    pointer-events: none;
`;

const Title = styled.div`
    font-weight: ${font.weight.bold};
    font-size: 32px;
    line-height: 36px;
    letter-spacing: -0.0011em;

    color: ${color.text.primary};
`;

const Subtitle = styled.div`
    font-weight: ${font.weight.medium};
    font-size: 16px;
    line-height: 22px;
    letter-spacing: -0.019em;

    color: ${color.text.secondary};

    margin-top: 10px;
`;

/* Pins container */
const SliderPinsContainer = styled.div`
    position: absolute;
    display: grid;
    grid-auto-flow: column;
    grid-gap: 6px;
    top: 483px;
    left: 50%;
    transform: translateX(-50%);
    pointer-events: none;
`;

/* Corns container */
const CornsContainer = styled.div`
    position: fixed;
    top: 0;
    left: 0;
    right: 0;
    bottom: 0;
    pointer-events: none;
    z-index: 1;
`;

interface CornProps {
    popcornStyle: FlattenSimpleInterpolation;
}

const Corn = styled.div<CornProps>`
    position: absolute;

    background-position: center;
    background-size: contain;
    background-repeat: no-repeat;

    ${({ popcornStyle }) => popcornStyle}
`;

interface PinProps {
    isActive: boolean;
}

const Pin = styled.div<PinProps>`
    background-color: #fff;
    border-radius: 1.5px;
    width: 12px;
    height: 3px;

    opacity: ${({ isActive }) => (isActive ? 1 : 0.16)};
    transition: opacity 0.5s ease-in-out;
`;

export const PromoTouch: React.FC = () => {
    const promoRef = useRef<HTMLDivElement>(null);
    const [promoWidth, setPromoWidth] = useState(0);
    const [currentSlideIndex, setCurrentSlideIndex] = useState(0);
    const cornsList = useMemo(() => popcornList.map((style, index) => <Corn key={index} popcornStyle={style} />), []);

    useEffect(() => {
        if (promoRef.current) {
            const { width } = promoRef.current.getBoundingClientRect();

            setPromoWidth(width);
        }
    }, []);

    return (
        <div ref={promoRef}>
            <CornsContainer>{cornsList}</CornsContainer>
            <TextContainer>
                <Title>Доставляем попкорн к просмотру фильмов</Title>
                <Subtitle>Бесплатно в течении 15-30 минут для Москвы и Санкт-Петербурга</Subtitle>
            </TextContainer>
            <SliderPinsContainer>
                {popcornBoxList.map((_, index) => (
                    <Pin key={index} isActive={index === currentSlideIndex} />
                ))}
            </SliderPinsContainer>
            <StyledSlider auto={5000} onSlideChange={(slide: number) => setCurrentSlideIndex(slide)} width={promoWidth}>
                {popcornBoxList.map((style, index) => (
                    <PopcornBox key={index} popcornStyle={style} />
                ))}
            </StyledSlider>
        </div>
    );
};
