import { DGBuildingSuggest } from 'tools/twoGis/api/suggest';
import { createAction, createEmptyAction } from 'tools/create-action';

export const startFetchingTwoGisSuggests = () => createEmptyAction('START_FETCHING_TWO_GIS_SUGGESTS');

export const finishFetchingTwoGisSuggests = (payload: DGBuildingSuggest[]) =>
    createAction('FINISH_FETCHING_TWO_GIS_SUGGESTS', payload);
