import React from 'react';
import styled, { css } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { addItemAction, removeItemAction } from 'store/actions/native';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { FootnoteText1, FootnoteText2 } from 'components/Text/Footnote@touch';
import { color } from 'style/const/color';
import MoreIcon from 'assets/icons/add.svg';
import LessIcon from 'assets/icons/remove.svg';

const StyledCartListItemTouch = styled.div`
    position: relative;
    padding: 10px 16px;
    margin-bottom: 4px;

    display: flex;
`;

const thumbSizePx = 40;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    position: relative;

    margin-right: 10px;

    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfo = styled.div`
    display: flex;
    flex-grow: 1;
    flex-direction: column;
    justify-content: center;
`;

const StyledName = styled(FootnoteText1)`
    display: -webkit-box;
    -webkit-box-orient: vertical;
    -webkit-line-clamp: 2;
    overflow: hidden;

    color: ${color.text.primary};
`;

const StyledPrice = styled(FootnoteText1)`
    margin-top: 2px;

    color: ${color.text.secondary};
`;

const StyledIndexBadge = styled(IndexBadge)`
    top: -4px;
    right: -4px;
`;

const StyledSelector = styled.div`
    display: flex;
    align-items: center;
    justify-content: space-between;

    margin-left: 16px;

    width: 100px;
`;

interface StyledCountTouchButtonProps {
    isDanger?: boolean;
}

const StyledCountTouchButton = styled.div<StyledCountTouchButtonProps>`
    cursor: pointer;
    background-color: rgba(255, 255, 255, 0.06);
    border-radius: 8px;
    display: flex;
    width: 32px;
    height: 32px;
    align-items: center;
    justify-content: center;
    text-align: center;
    font-size: 14px;

    ${({ isDanger }) =>
        isDanger &&
        css`
            background-color: ${color.button.critical};
        `}
`;

type StyledIconProps = {
    icon: string;
};

const StyledIcon = styled.div<StyledIconProps>`
    height: 12px;
    width: 12px;

    background-image: url(${({ icon }) => icon});
    background-repeat: no-repeat;
    background-size: contain;
`;

const StyledRemoveIcon = styled(StyledIcon)`
    transform: rotate(45deg);
    height: 15px;
    width: 15px;
`;

const StyledCount = styled(FootnoteText2)`
    color: ${color.text.primary};
`;

export interface CartListItemTouchProps {
    id: string;
    index: number;
}

export interface CartListItemTouchViewProps {
    imageUrl: string;
    name: string;
    price: number;
    count: number;
    disabled?: boolean;
    isMoreButtonDisabled?: boolean;
}

export const CartListItemTouch: React.FC<CartListItemTouchProps> = ({ index, id }) => {
    const { imageUrl, name, price, count, disabled } = useSelector(({ cartPageTouch: { itemsMap } }) => itemsMap[id]);

    const dispatch = useDispatch();

    const handleLessClick = useDebouncedOnceFn(
        () => {
            dispatch(
                removeItemAction({
                    item: id,
                    count: '1',
                    shouldRemoveMention: !count,
                }),
            );
        },
        debounceTimeoutMs,
        [dispatch, id, count],
    );

    const handleMoreClick = useDebouncedOnceFn(
        () => {
            dispatch(
                addItemAction({
                    item: id,
                    count: '1',
                }),
            );
        },
        debounceTimeoutMs,
        [dispatch, id],
    );

    return (
        <StyledCartListItemTouch>
            <StyledThumb imageUrl={imageUrl}>
                <StyledIndexBadge size="xxs" index={index + 1} />
            </StyledThumb>
            <StyledInfo>
                <StyledName>{name}</StyledName>
                <StyledPrice>{price} ₽</StyledPrice>
            </StyledInfo>
            {disabled ? null : (
                <StyledSelector>
                    <StyledCountTouchButton isDanger={!count} onClick={handleLessClick}>
                        {count ? <StyledIcon icon={LessIcon} /> : <StyledRemoveIcon icon={MoreIcon} />}
                    </StyledCountTouchButton>
                    <StyledCount>{count}</StyledCount>
                    <StyledCountTouchButton onClick={handleMoreClick}>
                        <StyledIcon icon={MoreIcon} />
                    </StyledCountTouchButton>
                </StyledSelector>
            )}
        </StyledCartListItemTouch>
    );
};
