import React from 'react';

export const useFocusOnChange = <T = HTMLElement>(
    forwardRef: React.RefObject<T> | undefined,
    hash: unknown,
    timeout = 0,
) => {
    const ref = React.useRef<T>(null);

    const onChangeFocusRef = forwardRef || ref;

    React.useEffect(() => {
        setTimeout(() => {
            if (hash && onChangeFocusRef.current instanceof HTMLElement) {
                onChangeFocusRef.current.focus();
            }
        }, timeout);
    }, [onChangeFocusRef, hash, timeout]);

    return onChangeFocusRef;
};
