import { AppReducer } from 'store/constants';
import { OrderStatus, OrderStateProductItem, mapOrderStatusToCategory } from 'store/constants/orderStatus';
import { formatPhone } from 'tools/phoneNumber';
import { LabelProps } from 'components/Label/Label';
import { StatusDetailsListItemViewProps } from 'pages/StatusDetails/ListItem/StatusDetailsListItem';

import {
    getFormattedPersonalAddress,
    getWrapperYShift,
    prepareTopGradientVisible,
    prepareBottomGradientVisible,
} from '../helpers/cartPage';

const itemHeight = 152;

const mapOrderStatusToSummaryText: Record<OrderStatus, string[]> = {
    [OrderStatus.INITIAL]: [''],
    [OrderStatus.INVOICE_CREATED]: ['Заказ создан, но не оплачен'],
    [OrderStatus.FUNDS_RESERVED]: ['Заказ создан и оплачен'],
    [OrderStatus.CONFIRMED_BY_PARTNER]: ['Заказ принят'],
    [OrderStatus.DELIVERING]: ['Заказ в доставке'],
    [OrderStatus.DELIVERED]: ['Заказ доставлен'],
    [OrderStatus.CANCEL_BY_PARTNER]: ['Заказ отменен'],
    [OrderStatus.CANCEL_BY_CUSTOMER]: ['Заказ отменен'],
    [OrderStatus.CANCEL_BY_PAYMENT]: ['Заказ отменен'],
    [OrderStatus.PAID]: ['Заказ доставлен'],
    [OrderStatus.CANCELLED]: ['Заказ отменен'],
    [OrderStatus.ERROR]: ['Ошибка обработки заказа'],
};

const getLabel = (item: OrderStateProductItem): LabelProps | undefined => {
    if (item.formedQuantity === 0) {
        return {
            type: 'sold-out',
        };
    }

    if (item.formedQuantity !== item.originalQuantity) {
        return {
            type: 'you-want',
            content: `${item.originalQuantity}`,
        };
    }

    if (item.formedPrice.value !== item.originalPrice.value) {
        return {
            type: 'price-changed',
            content: `${item.originalPrice.value}`,
        };
    }

    return undefined;
};

const getOrder = (productList: OrderStateProductItem[]) =>
    [...productList]
        .map((item): [string, number] => {
            let score = 0;

            score += 100 * Number(item.formedQuantity === 0);
            score +=
                10 *
                Number(
                    item.formedQuantity !== item.originalQuantity ||
                        item.formedPrice.value !== item.originalPrice.value,
                );

            return [item.productId, score];
        })
        .sort(([, scoreA], [, scoreB]) => scoreB - scoreA)
        .map(([productId]) => productId);

const getItemsMap = (productList: OrderStateProductItem[]) =>
    productList
        .map((item) => ({
            productId: item.productId,
            imageUrl: item.imageUrl,
            name: item.name,
            price: item.formedPrice.value,
            oldPrice: item.formedPrice.value !== item.originalPrice.value ? item.originalPrice.value : undefined,
            count: item.formedQuantity,
            label: getLabel(item),
            disabled: item.formedQuantity === 0,
        }))
        .reduce((acc, item) => {
            acc[item.productId] = item;
            return acc;
        }, {} as Record<string, StatusDetailsListItemViewProps>);

export const statusPageView: AppReducer = (state, action) => {
    const { orderStatusSummaryPage, orderStatusReportPage, orderStatusDetailsPage, orderStatusData, userData } = state;
    const { orderStatus, reportPhone, productList, formedTotalPrice } = orderStatusData;
    const { phoneNumber } = userData;

    switch (action.type) {
        case 'statusDetails/SELECT_ITEM': {
            orderStatusDetailsPage.selectedItemId = action.payload;
            break;
        }

        default: {
            break;
        }
    }

    // summary page

    orderStatusSummaryPage.isLoaded = orderStatusData.orderStatus !== OrderStatus.INITIAL;
    orderStatusSummaryPage.title = mapOrderStatusToSummaryText[orderStatus][0] ?? '';
    orderStatusSummaryPage.text = mapOrderStatusToSummaryText[orderStatus][1] ?? '';
    orderStatusSummaryPage.orderStatusCategory = mapOrderStatusToCategory[orderStatus];

    // report page

    orderStatusReportPage.phoneNumber = formatPhone(reportPhone);

    // details page

    orderStatusDetailsPage.order = getOrder(productList);
    orderStatusDetailsPage.itemsMap = getItemsMap(productList);
    orderStatusDetailsPage.totalPrice = formedTotalPrice.value;

    orderStatusDetailsPage.phoneNumber = formatPhone(phoneNumber);
    orderStatusDetailsPage.personalAddress = getFormattedPersonalAddress(state);

    orderStatusDetailsPage.selectedItemIndex = orderStatusDetailsPage.order.indexOf(
        orderStatusDetailsPage.selectedItemId,
    );

    orderStatusDetailsPage.wrapperYShift = getWrapperYShift(
        orderStatusDetailsPage.selectedItemIndex,
        orderStatusDetailsPage.order.length,
        itemHeight,
    );

    orderStatusDetailsPage.topGradientVisible = prepareTopGradientVisible(orderStatusDetailsPage.wrapperYShift);
    orderStatusDetailsPage.bottomGradientVisible = prepareBottomGradientVisible(
        orderStatusDetailsPage.wrapperYShift,
        orderStatusDetailsPage.order.length,
        itemHeight,
    );
};
