/* eslint-disable @typescript-eslint/no-explicit-any */
interface Fn<R = any> {
    (...args: any): R;
}

export const memoize = <F extends Fn>(fn: F): F => {
    const store = new Map<string, ReturnType<F>>();

    return ((...args) => {
        const id = JSON.stringify(args);

        if (!store.has(id)) {
            store.set(id, fn(...args));
        }

        return store.get(id);
    }) as F;
};

export const memoizeResolvedPromise = <F extends Fn<Promise<any>>>(fn: F): F => {
    const store = new Map<string, ReturnType<F>>();

    return (async (...args) => {
        const id = JSON.stringify(args);

        if (!store.has(id)) {
            const result = await fn(...args);
            store.set(id, result);
        }

        return store.get(id);
    }) as F;
};
