import React from 'react';
import { useLottie, LottieConfig, Lottie } from 'react-lottie-hook';
import styled, { css } from 'styled-components';
import { transition } from 'style/const/transition';

interface StyledSpinnerProps {
    isPaused: boolean;
}

const StyledSpinner = styled(Lottie)<StyledSpinnerProps>`
    position: relative;

    opacity: 0;

    ${({ isPaused }) =>
        !isPaused &&
        css`
            opacity: 1;
            transition: opacity ${transition.default};
        `}
`;

const defaultConfig: LottieConfig = {
    renderer: 'svg',
    loop: true,
    autoplay: false,
};

interface SplendidAnimationProps {
    width: number;
    height: number;
    config: LottieConfig;
    delayMs?: number;
}

export const SplendidAnimation: React.FC<SplendidAnimationProps> = ({
    width,
    height,
    config,
    delayMs = 0,
    ...props
}) => {
    const lottieOptions = React.useMemo(
        () => ({
            ...defaultConfig,
            ...config,
        }),
        [config],
    );

    const [lottieRef, state, { pause, play }] = useLottie(lottieOptions);

    // эта магия нужна, потому что Lottie может достаточно долго инициализироватся,
    // чтобы иногдаморгать первым кадром анимации.
    // поэтому изначально мы скрываем анимацию, а после инициализации показываем

    const [isDelayed, setDelayed] = React.useState(true);

    const start = React.useCallback(() => {
        setDelayed(false);
        play();
    }, [setDelayed, play]);

    React.useLayoutEffect(() => {
        pause();
        const timeoutId = setTimeout(start, delayMs);

        return () => {
            pause();
            clearTimeout(timeoutId);
        };
    }, [pause, start, delayMs]);

    return (
        <StyledSpinner
            lottieRef={lottieRef}
            isPaused={isDelayed || state.isPaused || state.isStopped}
            width={width}
            height={height}
            {...props}
        />
    );
};
