import React from 'react';
import styled from 'styled-components';
import { Provider as ReduxProvider } from 'react-redux';
import { Switch, Route } from 'react-router';
import { ConnectedRouter } from 'connected-react-router';
import { history } from 'tools/history';
import { store } from 'store';
import { ROUTES } from 'store/constants/router';
import { AppLayout } from 'components/AppLayout/AppLayout';
import { Catalog } from 'pages/Catalog/Catalog';
import { Cart } from 'pages/Cart/Cart';
import { Success } from 'pages/Success/Success';
import { Promo } from 'pages/Promo/Promo';
import { NoArea } from 'pages/NoArea/NoArea';
import { AddressSimple } from 'pages/AddressSimple/AddressSimple';
import { AddressSimpleGuide } from 'pages/AddressSimpleGuide/AddressSimpleGuide';
import { AddressSimpleQuestion } from 'pages/AddressSimpleQuestion/AddressSimpleQuestion';
import { AddressSimpleInput } from 'pages/AddressSimpleInput/AddressSimpleInput';
import { AddressFullAll } from 'pages/AddressFullAll/AddressFullAll';
import { AddressFullApartment } from 'pages/AddressFullApartment/AddressFullApartment';
import { AddressFullDoorCode } from 'pages/AddressFullDoorCode/AddressFullDoorCode';
import { AddressFullEntrance } from 'pages/AddressFullEntrance/AddressFullEntrance';
import { AddressFullFloor } from 'pages/AddressFullFloor/AddressFullFloor';
import { AddressFullPhone } from 'pages/AddressFullPhone/AddressFullPhone';
import { AddressFullAccommodationGuide } from 'pages/AddressFullAccommodationGuide/AddressFullAccommodationGuide';
import { AddressFullPhoneGuide } from 'pages/AddressFullPhoneGuide/AddressFullPhoneGuide';
import { Error } from 'pages/Error/Error';
import { Wait } from 'pages/Wait/Wait';
import { StatusSummary } from 'pages/StatusSummary/StatusSummary';
import { StatusDetails } from 'pages/StatusDetails/StatusDetails';
import { StatusReport } from 'pages/StatusReport/StatusReport';
import { StatusEmpty } from 'pages/StatusEmpty/StatusEmpty';

import 'style/global.scss';

const StyledContainer = styled.div`
    overflow: hidden;
    width: 100%;
    height: 100vh;
    outline: none;
    font-family: 'SB Sans Text', Helvetica, Arial, sans-serif;
    background: #000;
`;

const preventScroll = (event: React.SyntheticEvent<HTMLDivElement>) => {
    event.currentTarget.scrollTop = 0;
};

export const RootDesktop: React.FC = () => (
    <ReduxProvider store={store}>
        <ConnectedRouter history={history}>
            <StyledContainer onScroll={preventScroll}>
                <AppLayout>
                    <Switch>
                        <Route path={ROUTES.CATALOG}>
                            <Catalog />
                        </Route>
                        <Route path={ROUTES.CART}>
                            <Cart />
                        </Route>
                        <Route path={ROUTES.SUCCESS}>
                            <Success />
                        </Route>
                        <Route path={ROUTES.STATUS_SUMMARY}>
                            <StatusSummary />
                        </Route>
                        <Route path={ROUTES.STATUS_DETAILS}>
                            <StatusDetails />
                        </Route>
                        <Route path={ROUTES.STATUS_REPORT}>
                            <StatusReport />
                        </Route>
                        <Route path={ROUTES.STATUS_EMPTY}>
                            <StatusEmpty />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE}>
                            <AddressSimple />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_GUIDE}>
                            <AddressSimpleGuide />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_QUESTION}>
                            <AddressSimpleQuestion />
                        </Route>
                        <Route path={ROUTES.ADDRESS_SIMPLE_INPUT}>
                            <AddressSimpleInput />
                        </Route>
                        <Route path={ROUTES.ADDRESS_FULL}>
                            <Switch>
                                <Route path={ROUTES.ADDRESS_FULL_ALL}>
                                    <AddressFullAll />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_PHONE}>
                                    <AddressFullPhone />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_APARTMENT}>
                                    <AddressFullApartment />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_ENTRANCE}>
                                    <AddressFullEntrance />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_FLOOR}>
                                    <AddressFullFloor />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_DOOR_CODE}>
                                    <AddressFullDoorCode />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_ACCOMMODATION_GUIDE}>
                                    <AddressFullAccommodationGuide />
                                </Route>
                                <Route path={ROUTES.ADDRESS_FULL_PHONE_GUIDE}>
                                    <AddressFullPhoneGuide />
                                </Route>
                            </Switch>
                        </Route>
                        <Route path={ROUTES.PROMO}>
                            <Promo />
                        </Route>
                        <Route path={ROUTES.NO_AREA}>
                            <NoArea />
                        </Route>
                        <Route path={ROUTES.ERROR}>
                            <Error />
                        </Route>
                        <Route path={ROUTES.WAIT}>
                            <Wait />
                        </Route>
                    </Switch>
                </AppLayout>
            </StyledContainer>
        </ConnectedRouter>
    </ReduxProvider>
);
