import React from 'react';

interface UseFocusInterface {
    onReceiveFocus?: () => void;
    onLoseFocus?: () => void;
}

/**
 * Срабатывает при получении/потери фокуса содержимым контейнера
 * Не срабатывает при переключении фокуса, пока он остается внутри контейнера
 */
export const useContainerFocus = ({ onReceiveFocus, onLoseFocus }: UseFocusInterface) => {
    const [handlerTimeoutId, setHandlerTimeoutId] = React.useState(0);
    const [isFocused, setFocused] = React.useState(false);

    const handleReceiveFocus = React.useCallback(() => {
        if (!isFocused) {
            setFocused(true);
            onReceiveFocus?.();
        }
    }, [isFocused, onReceiveFocus]);

    const handleLoseFocus = React.useCallback(() => {
        if (isFocused) {
            setFocused(false);
            onLoseFocus?.();
        }
    }, [isFocused, onLoseFocus]);

    /**
     * Планируем срабатывание handleReceiveFocus при получении фокуса контейнером или его дочерним элементом
     */
    const handleFocus = React.useCallback(() => setHandlerTimeoutId(setTimeout(handleReceiveFocus)), [
        handleReceiveFocus,
    ]);

    /**
     * Планируем срабатывание handleLoseFocus при потере фокуса контейнером или его дочерним элементом
     */
    const handleBlur = React.useCallback(() => setHandlerTimeoutId(setTimeout(handleLoseFocus)), [handleLoseFocus]);

    /**
     * Cбрасываем запланированные обработчики при планировании нового
     */
    React.useEffect(() => {
        const timeoutId = handlerTimeoutId;
        return () => {
            if (timeoutId) {
                clearTimeout(timeoutId);
            }
        };
    }, [handlerTimeoutId]);

    return {
        handleFocus,
        handleBlur,
        isFocused,
    };
};
