import React from 'react';
import styled from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { Pin } from 'components/Pin/Pin@touch';
import { ParagraphText2 } from 'components/Text/Paragraph@touch';
import { CaptionText } from 'components/Text/Caption';
import { HeadlineText4 } from 'components/Text/Headline@touch';
import { BodyText1 } from 'components/Text/Body@touch';
import { color } from 'style/const/color';
import { font } from 'style/const/font';

import bgImage from '../Cart.assets/bg@touch.jpg';

const StyledBackground = styled.div`
    position: absolute;
    top: -65px;
    left: 0;
    right: 0;
    bottom: 0;

    background-image: url('${bgImage}');

    opacity: 0.6;
`;

const StyledCartInfoTouch = styled.div`
    position: relative;

    height: 236px;

    padding: 15px 16px 0;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;

    &::before {
        content: '';
        position: absolute;
        left: 0;
        right: 0;
        bottom: 0;
        height: 96px;
        background: linear-gradient(360deg, #080808 0%, rgba(8, 8, 8, 0) 100%);

        z-index: 1;

        pointer-events: none;
    }
`;

const StyledPlaceInfo = styled.div`
    position: relative;
    z-index: 1;
`;

const StyledTime = styled(ParagraphText2)`
    font-weight: ${font.weight.semibold};
    color: ${color.text.primary};
`;

const StyledUnit = styled(CaptionText)`
    margin-top: -4px;

    color: ${color.text.primary};
`;

const StyledAddress = styled(HeadlineText4)`
    margin-top: 14px;

    display: flex;
    flex-direction: column;
    align-items: center;

    -webkit-line-clamp: 2;

    color: ${color.text.primary};
    text-align: center;
`;

const StyledPersonal = styled(BodyText1)`
    margin-top: 6px;

    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
`;

const StyledPersonalLocation = styled(BodyText1)`
    -webkit-line-clamp: 2;

    color: ${color.text.secondary};
`;

export const CartInfoTouch: React.FC = () => {
    const { address } = useSelector(({ userData }) => userData);
    const { deliveryTime, hasRequiredData, personalAddress } = useSelector(({ cartPageTouch }) => cartPageTouch);

    return (
        <StyledCartInfoTouch>
            <StyledPlaceInfo>
                <Pin>
                    <StyledTime>{deliveryTime}</StyledTime>
                    <StyledUnit size={12} lineHeight={16}>
                        мин
                    </StyledUnit>
                </Pin>
                <StyledAddress>{address}</StyledAddress>
                {hasRequiredData ? (
                    <StyledPersonal>
                        <StyledPersonalLocation>{personalAddress}</StyledPersonalLocation>
                    </StyledPersonal>
                ) : null}
            </StyledPlaceInfo>
            <StyledBackground />
        </StyledCartInfoTouch>
    );
};
