import { doRequest } from 'tools/request';

import { DGApiResponse } from './common';

const apiKey = process.env.REACT_APP_ENVIRONMENT === 'production' ? 'rukrby5549' : 'rusrav6003';

const apiPrefix = 'https://catalog.api.2gis.ru';

export const doDGApiRequest = async <Params, ResponseType>(
    url: string,
    params: Params,
): Promise<DGApiResponse<ResponseType>> => {
    const response = await doRequest<DGApiResponse<ResponseType>>(`${apiPrefix}${url}`, {
        key: apiKey,
        ...params,
    });

    return response;
};
