import { Button, ButtonSize, ButtonTextAlign, ButtonViews } from 'components/Button/Button';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { buildAddressString } from 'pages/AddressSimple/AddressSimple.tools';
import React from 'react';
import { addressSubmitThunk } from 'store/thunks/native';
import styled, { css, keyframes } from 'styled-components';
import { useSelector } from 'hooks/useSelector';
import { useDispatch } from 'hooks/useDispatch';
import { addressSelectSuggestItemAction } from 'store/actions/addressSimplePage';

interface StyledButtonProps {
    isFlashing?: boolean;
}
const StyledButton = styled(Button)<StyledButtonProps>`
    width: 100%;
    padding: 36px 40px;

    ${({ isFlashing }) =>
        isFlashing &&
        css`
            &::after {
                animation-name: ${keyframes`
                    0% {
                        opacity: 1;
                    }
                    50% {
                        opacity: 0;
                    }
                    100% {
                        opacity: 1;
                    }
                `};
                animation-iteration-count: infinite;
                animation-timing-function: linear;
                animation-duration: 1.5s;
            }
        `}
`;

interface SuggestListItemProps {
    id: string;
    shouldFocusOnMount?: boolean;
}
export const AddressSimpleSuggestListItem: React.FC<SuggestListItemProps> = ({ id, shouldFocusOnMount }) => {
    const suggest = useSelector(({ twoGisSuggestsData: { itemsMap } }) => itemsMap[id]);
    const selectedSuggestItemId = useSelector(({ addressSimplePage }) => addressSimplePage.selectedSuggestItemId);
    const isAddressSubmitting = useSelector(({ addressSimplePage }) => addressSimplePage.isAddressSubmitting);
    const isSelectedItem = selectedSuggestItemId === id;
    const isDisabled = isAddressSubmitting && !isSelectedItem;
    const isFlashing = isAddressSubmitting && isSelectedItem;
    const dispatch = useDispatch();
    const handleClick = useDebouncedOnceFn(
        () => {
            if (isAddressSubmitting) {
                return;
            }

            dispatch(addressSubmitThunk());
        },
        debounceTimeoutMs,
        [dispatch, isAddressSubmitting],
    );
    const handleFocus = React.useCallback(() => {
        dispatch(addressSelectSuggestItemAction(id));
    }, [dispatch, id]);

    const addressName = suggest && buildAddressString(suggest);

    return (
        <StyledButton
            shouldFocusOnMount={shouldFocusOnMount}
            disabled={isDisabled && !isFlashing}
            isFlashing={isFlashing}
            onFocus={handleFocus}
            onClick={handleClick}
            view={ButtonViews.secondary}
            size={ButtonSize.l}
            textAlign={ButtonTextAlign.left}
        >
            {addressName}
        </StyledButton>
    );
};
