import { createHashHistory } from 'history';

export const history = createHashHistory();

/**
 * Возвращает промис, который резолвится при следующем изменении стории.
 * Это нужно для того, чтобы можно было "синхронно" вызвать несколько изменений истории.
 *
 * Например, если вызвать go(-2) и replace('some-url') последовательно, то второй вызов
 * не отработает, потому что изменение истории работает асинхронно. Поэтому мы вызываем
 * первый метод, ждем пока прилетит событие и вызываем второй.
 */
export const getWaitNextNavigationEventPromise = () => {
    return new Promise((resolve) => {
        let unListen: () => void;

        const handleLocationChange = () => {
            if (unListen) {
                unListen();
            }

            resolve();
        };

        unListen = history.listen(handleLocationChange);
    });
};
