import { AppReducer } from 'store/constants';

export const waitPageView: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ASK_FOR_LAST_ORDER_INFO': {
            state.waitPage.text = 'Обновляем\n статус заказа';
            break;
        }
        case 'GO_TO_WAIT':
        case 'ASK_AGAIN_FOR_ORDER_STATUS':
        case 'MAKE_PURCHASE': {
            state.waitPage.text = 'Оформляем\n заказ';
            break;
        }
        case 'LAST_ORDER_ERROR':
        case 'LAST_ORDER_INFO':
        case 'LAST_ORDER_EMPTY':
        case 'VALIDATION_OK':
        case 'ORDER_ERROR': {
            state.waitPage.text = '';
            break;
        }
        default: {
            break;
        }
    }
};
