import { DGBuildingSuggest } from 'tools/twoGis/api/suggest';

export const buildAddressString = (suggest: DGBuildingSuggest) => {
    // eslint-disable-next-line @typescript-eslint/camelcase
    const { address, full_name } = suggest;
    /**
     * этот ифак скорее тайпгвард
     * саджесты без компонентов адреса фильтруются в самом начале
     */
    if (!address.components || !address.components.length) {
        return;
    }

    const [addressComponent] = address.components;
    const { street, number } = addressComponent;
    // eslint-disable-next-line @typescript-eslint/camelcase
    const [city] = full_name.split(', ');

    return [city, street, number].join(', ');
};
