const serverActionMiddleware: AppMiddleware = ({ dispatch, getState }) => (next) => (action) => {
    switch (action.type) {
        case 'CLOSE_APP': {
            window.AssistantClient.close();
            break;
        }
        case 'ASK_FOR_STOCK': {
            window.AssistantClient.sendData({ action_id: action.type });

            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: SHOW_ITEMS_MOCK,
                    });
                }, 500);

                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: '21ff407a-fa98-11e8-80c5-0cc47a817925',
                            count: '5',
                        },
                    });
                }, 1000);

                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'ADD_ITEM',
                            item: '21ff407c-fa98-11e8-80c5-0cc47a817925',
                            count: '2',
                        },
                    });
                }, 1500);
            }

            break;
        }
        case 'ASK_FOR_ITEMS_CHANGES': {
            window.AssistantClient.sendData({ action_id: action.type });

            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line
                        smart_app_data: ITEMS_CHANGES_MOCK,
                    });
                }, 500);
            }

            break;
        }
        case 'ADDRESS_SUBMIT': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData(GO_TO_CATALOG_COMMAND);
                }, 500);
            }

            break;
        }
        case 'ADDRESS_FULL_SUBMIT': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'GO_TO_VALIDATION',
                    },
                });
            }

            break;
        }
        case 'ASK_FOR_ORDER_STATUS': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'ASK_AGAIN_FOR_ORDER_STATUS_FINAL',
                    },
                });
            }

            break;
        }
        case 'ASK_FOR_ORDER_STATUS_FINAL': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                window.AssistantClient.onData({
                    type: 'smart_app_data',
                    // eslint-disable-next-line @typescript-eslint/camelcase
                    smart_app_data: {
                        command: 'VALIDATION_OK',
                        status: true,
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        order_id: '364528',
                        date: '2020-05-11T10:35:08.003578+03:00',
                    },
                });
            }

            break;
        }
        case 'MAKE_PURCHASE': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'GO_TO_WAIT',
                        },
                    });
                }, 500);
            }

            break;
        }
        case 'ASK_FOR_LAST_ORDER_INFO': {
            window.AssistantClient.sendData({ action_id: action.type });

            // эмуляция ответа бэка для отладки в браузере
            if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                setTimeout(() => {
                    window.AssistantClient.onData({
                        type: 'smart_app_data',
                        // eslint-disable-next-line @typescript-eslint/camelcase
                        smart_app_data: {
                            command: 'LAST_ORDER_INFO',
                            info: lastOrderInfoMock,
                        },
                    });
                }, 500);
            }

            break;
        }
        case 'PAGE_CHANGE': {
            window.AssistantClient.sendData({ action_id: action.type });
            break;
        }
        /**
         * tldr; В кейсе LOCATION_CHANGE все dispatch'и нужно ОБЯЗАТЕЛЬНО оборачивать в setTimeout.
         * Нельзя влазить и диспатчить action'ы перед LOCATION_CHANGE, от этого ломается
         * взаимодействие connected-react-router и history.
         *
         * Внимание! Перед прочтением этого коммента лучше пойти взять бутылку чего нибудь покрепче,
         * потому что без нее не разобраться.
         *
         * Так вот, для понимания того, почему это нужно делать. Сперва, нужно понять то,
         * как работает навигация в кишках connected-react-router и history.
         * Предположим, мы хотим перейти на другую страницу. Для этого
         * dispatch'им push action react-connected-router'a с типом @@router/CALL_HISTORY_METHOD
         * который подхватывает router middleware и вызывает
         * соответствующий метод (push) у библиотеки history.
         * В результате чего history меняет урл через History API браузера
         * и обновляет свой стейт. После оповещает слушателей, подписанных на это.
         * Внутри connected-react-router'а вызывается обработчик, который подписан
         * на изменение стейта (локального, внутри библиотеки) history.
         * Он dispatch'ит action, с типом @@router/LOCATION_CHANGE, который должен долететь
         * до редюсера connected-react-router’а и актуализировать (синкнуть со стейтом в history) стейт router'a.
         * После чего на изменение стейта срабатывает обработчик который обычно триггерит рендер react'а,
         * но у connected-react-router’a есть свой такой, который нужен для того,
         * чтобы синкать стейт router'a в сторе и стейт пакета history.
         * То есть в обратную сторону. Вот в этом то месте и происходит рассинхрон.
         *
         * Из-за того что этот обработчик в connected-react-router'е не смотрит на action,
         * который к нему прилетает (redux его не прокидывает), он пытается синкать
         * состояния на каждый чих. Валидно убеждается в том, что состояния разные из-за того что history
         * уже внесла в свой стейт изменения, а редюсер router'a еще не отработал, потому что
         * до него влез другой action.
         * После чего вызывает соответствующий метод у history, который снова меняет ее состояние
         * и снова триггерит об этом экшен и все это еще больше запутывается.
         **/
        case LOCATION_CHANGE: {
            const state = getState();

            const currentPathname = selectPathname(state);
            const nextPathname = action.payload.location.pathname;

            /**
             * Кейс LOCATION_CHANGE отвечает за сайд эффекты во время переходов
             * между экранами. Может случиться так что реплейсится текущий экран
             * (если мы с каталога пошли в ввода адреса, а потом вычистили историю),
             * и в этом случае делать ничего не нужно.
             */
            if (currentPathname === nextPathname) {
                break;
            }

            switch (currentPathname) {
                case ROUTES.CATALOG:
                case ROUTES.CART: {
                    setTimeout(() => {
                        dispatch(applyItemsChanges());
                    });

                    break;
                }
                case ROUTES.ADDRESS_SIMPLE:
                case ROUTES.ADDRESS_SIMPLE_QUESTION: {
                    /**
                     * Если мы возвращаемся обратно на страницу ввода
                     * полного адреса со страницы ввода базового адреса,
                     * без сохранения, то нужно вернуть все поля в исходное
                     * положение, но есть нюанс ;)
                     */
                    if (nextPathname === ROUTES.ADDRESS_FULL_ALL) {
                        /**
                         *  Если удалить один символ в инпуте, а потом ввести его,
                         *  то адрес не поменяется (строковое значение), но все остальные поля
                         *  (дом, улица, город) будут сброшены (потому что они сбрасываются при
                         *  изменении значения инпуте).
                         *  Поэтому здесь так же есть проверка на заполненность.
                         */
                        const isAddressChanged = state.userData.address !== state.userModel.address;
                        const hasValidAddress = getHasValidAddress(state.userModel);
                        if (!hasValidAddress || isAddressChanged) {
                            setTimeout(() => {
                                dispatch(revertUserChanges());
                            });
                        }
                    }

                    break;
                }
                case ROUTES.ADDRESS_FULL_ALL: {
                    if (nextPathname === ROUTES.CART) {
                        setTimeout(() => {
                            dispatch(revertUserChanges());
                        });
                    }
                    break;
                }

                default:
                    break;
            }

            switch (nextPathname) {
                case ROUTES.CATALOG: {
                    if (Object.keys(state.productsData.itemsMap).length) {
                        setTimeout(() => {
                            dispatch(askForItemsChangesAction());
                        });
                    } else {
                        setTimeout(() => {
                            dispatch(askForStockAction());
                        });
                    }

                    break;
                }
                case ROUTES.CART: {
                    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
                        if (!Object.keys(state.productsData.itemsMap).length) {
                            setTimeout(() => {
                                dispatch(askForStockAction());
                            });
                        }
                    }

                    setTimeout(() => {
                        dispatch(askForItemsChangesAction());
                    });

                    break;
                }

                case ROUTES.STATUS_SUMMARY: {
                    setTimeout(() => {
                        dispatch(askForLastOrderInfoAction());
                    });

                    break;
                }

                default:
                    break;
            }

            // при ините приложения прилетает LOCATION_CHANGE,
            // это проверка нужна чтобы не слать лишний сервер экшен
            if (currentPathname !== nextPathname) {
                setTimeout(() => {
                    dispatch(pageChangeAction());
                });
            }

            break;
        }
        default:
            break;
    }

    return next(action);
};

const enhancers: StoreEnhancer[] = [];
const middlewares: Middleware[] = [thunk, routerMiddleware(history), serverActionMiddleware as Middleware];

if (process.env.NODE_ENV !== 'production') {
    if (typeof window === 'object' && window.__REDUX_DEVTOOLS_EXTENSION__) {
        enhancers.push(window.__REDUX_DEVTOOLS_EXTENSION__());
    }
}

export const store = createReduxStore<AppState, AppBaseAction, {}, {}>(
    /**
     * TODO: прикрутить валидацию recoveryStat'a
     * @see https://sbtatlas.sigma.sbrf.ru/jira/browse/FOODTECHUI-200
     */
    getRootReducer(history, window.appRecoveryState as AppState),
    compose(applyMiddleware(...middlewares), ...enhancers),
);
const dispatch = store.dispatch as AppDispatch;

if (process.env.NODE_ENV !== 'production') {
    window.__store = store;
}

export const dispatchToReduxStore = ({ smart_app_data, type }: AssistantSdkCommand) => {
    if (type !== 'smart_app_data') {
        return;
    }

    switch (smart_app_data.command) {
        case 'SHOW_ITEMS':
            dispatch(showItemsAction(smart_app_data));
            break;
        case 'ITEMS_CHANGES': {
            dispatch(itemsChangesAction(smart_app_data));
            break;
        }
        case 'ADD_ITEM': {
            dispatch(addItemAction(smart_app_data));
            break;
        }
        case 'REMOVE_ALL': {
            dispatch(removeAllAction());
            break;
        }
        case 'REMOVE_ITEM': {
            dispatch(removeItemAction({ ...smart_app_data, shouldRemoveMention: true }));
            break;
        }
        case 'GO_TO_CATALOG': {
            dispatch(goToCatalogThunk(smart_app_data));
            break;
        }
        case 'GO_TO_NO_AREA': {
            dispatch(goToNoAreaThunk());
            break;
        }
        case 'GO_TO_VALIDATION': {
            dispatch(goToValidationThunk());
            break;
        }
        case 'GO_TO_PROMO': {
            dispatch(goToPromoThunk(smart_app_data));
            break;
        }
        case 'GO_TO_ADDRESS_SIMPLE': {
            dispatch(push(ROUTES.ADDRESS_SIMPLE));
            break;
        }
        case 'GO_TO_ADDRESS_FULL': {
            dispatch(goToAddressFullAllThunk());
            break;
        }
        case 'VALIDATION_OK': {
            dispatch(validationOkThunk(smart_app_data));
            break;
        }
        case 'ORDER_ERROR': {
            dispatch(orderErrorAction(smart_app_data));
            dispatch(replace(ROUTES.ERROR));
            break;
        }
        case 'ADDRESS_GUIDE': {
            dispatch(addressGuideThunk());
            break;
        }
        case 'ADDRESS_INPUT': {
            dispatch(addressInputThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_INPUT_ADD': {
            dispatch(addressInputAddThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_INPUT_DECLINE': {
            dispatch(addressInputDeclineThunk());
            break;
        }
        case 'ADDRESS_INPUT_CLEAR': {
            dispatch(addressInputClearAction());
            break;
        }
        case 'ADDRESS_FULL_SUCCESS': {
            dispatch(addressFullSuccessThunk());
            break;
        }
        case 'ADDRESS_FULL_INPUT': {
            dispatch(addressFullInputThunk(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_ADD': {
            dispatch(addressFullInputAddAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_INPUT_CLEAR': {
            dispatch(addressFullInputClearAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_COMMENT': {
            dispatch(addressFullCommentAction(smart_app_data));
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_OK': {
            dispatch(addressFullValidationOkThunk());
            break;
        }
        case 'ADDRESS_FULL_VALIDATION_ERROR': {
            dispatch(addressFullValidationErrorAction());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS_FINAL': {
            dispatch(askAgainForOrderStatusFinalAction());
            setTimeout(() => dispatch(askForOrderStatusFinalAction()), 500);
            break;
        }
        case 'GO_TO_WAIT': {
            dispatch(push(ROUTES.WAIT));
            dispatch(goToWaitAction());
            dispatch(askForOrderStatusAction());
            break;
        }
        case 'GO_BACK': {
            dispatch(goBackAction());
            dispatch(goBack());
            break;
        }
        case 'ASK_AGAIN_FOR_ORDER_STATUS': {
            dispatch(askAgainForOrderStatusAction());
            setTimeout(() => dispatch(askForOrderStatusAction()), 500);
            break;
        }
        case 'GO_TO_ORDER_SEARCH': {
            dispatch(replace(ROUTES.STATUS_SUMMARY));
            break;
        }
        case 'LAST_ORDER_INFO': {
            dispatch(lastOrderInfoAction(smart_app_data));
            break;
        }
        case 'LAST_ORDER_EMPTY': {
            dispatch(lastOrderEmptyAction());
            dispatch(goBackToStartAndReplaceThunk(ROUTES.STATUS_EMPTY));
            break;
        }
        case 'LAST_ORDER_ERROR': {
            dispatch(lastOrderErrorAction());
            dispatch(push(ROUTES.ERROR));
            break;
        }
        case 'GO_TO_LAST_ORDER_DETAILS': {
            dispatch(push(ROUTES.STATUS_DETAILS));
            break;
        }
        case 'GO_TO_LAST_ORDER_REPORT': {
            dispatch(push(ROUTES.STATUS_REPORT));
            break;
        }
        default:
            break;
    }
};

if (process.env.REACT_APP_ENVIRONMENT !== 'production' && module.hot) {
    module.hot.accept('./reducers', () => {
        // eslint-disable-next-line
        const { getRootReducer } = require('./reducers');
        const rootReducer = getRootReducer(history);

        store.replaceReducer(rootReducer);
    });
}

interface ItemSelectorItem {
    id: string;
    title: string;
    visible: boolean;
    number?: number;
}

const castCatalogItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        catalogPage: { screenIndexMap },
        productsData: { itemsMap },
    } = state;

    return {
        ignored_words: ['добавь', 'удали'],
        items: Object.values(itemsMap).map(
            ({ id, name: title }): ItemSelectorItem => {
                const number = screenIndexMap[id];

                return {
                    id,
                    title,
                    number,
                    visible: Boolean(number),
                };
            },
        ),
    };
};

const castCartItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const {
        cart: { order },
        productsData: { itemsMap },
    } = state;

    return {
        ignored_words: ['добавь', 'удали'],
        items: Object.values(itemsMap).map(
            ({ id, name: title }): ItemSelectorItem => {
                const number = order.indexOf(id) + 1 || undefined;

                return {
                    id,
                    title,
                    number,
                    visible: Boolean(number),
                };
            },
        ),
    };
};

const castItemSelectorFromStore = (state: AppState): AppStateInterface['item_selector'] => {
    const pathname = selectPathname(state);

    switch (pathname) {
        case ROUTES.CATALOG:
            return castCatalogItemSelectorFromStore(state);

        case ROUTES.CART:
            return castCartItemSelectorFromStore(state);

        default:
            return {
                ignored_words: [],
                items: [],
            };
    }
};

const serializeAddress = (storeUser: StoreUser): ScenarioUserAddress => {
    const {
        address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        doorCode,
        addressLocationLatitude,
        addressLocationLongitude,
    } = storeUser;

    return {
        location: {
            latitude: addressLocationLatitude,
            longitude: addressLocationLongitude,
        },
        address_string: address,
        city,
        street,
        house,
        building,
        floor,
        entrance,
        apartment,
        door_code: doorCode,
        confirmed: 'true',
    };
};

const prepareAddress = (state: AppState): ScenarioUser => {
    let source;

    switch (selectPathname(state)) {
        case ROUTES.ADDRESS_SIMPLE:
        case ROUTES.ADDRESS_SIMPLE_QUESTION:
        case ROUTES.ADDRESS_FULL_ALL: {
            source = state.userModel;

            break;
        }

        default: {
            source = state.userData;

            break;
        }
    }

    return {
        phone_num: source.phoneNumber,
        customer_name: source.customerName,
        address: serializeAddress(source),
    };
};

const castFromStore = (): AppStateInterface => {
    const state = store.getState();
    const { cart } = state;
    const pathname = selectPathname(state);

    return {
        item_selector: castItemSelectorFromStore(state),
        foodtech: {
            currentScreen: pathname.substring(1),
            products: Object.keys(cart.itemsMap)
                .map((id) => {
                    const quantity = getCountById(state, id);

                    return { id, quantity };
                })
                .filter(({ quantity }) => Boolean(quantity)),
            totalPrice: {
                value: cart.totalPrice,
                discountValue: cart.totalPrice,
            },
            ...prepareAddress(state),
        },
    };
};

if (window.appInitialData) {
    if (Array.isArray(window.appInitialData)) {
        window.appInitialData.forEach(dispatchToReduxStore);
    } else {
        dispatchToReduxStore(window.appInitialData);
    }
}

/* eslint-disable no-console */
window.AssistantClient = window.AssistantClient || {};

if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
    // eslint-disable-next-line no-underscore-dangle
    window.__inbox = window.__inbox || [];
    // eslint-disable-next-line no-underscore-dangle
    window.__outbox = window.__outbox || [];
}

/**
 * Assistand SDK API implementation:
 */
window.AssistantClient.onStart = () => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke onStart callback');
    }
};

window.AssistantClient.onData = (command: AssistantSdkCommand) => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        // eslint-disable-next-line no-underscore-dangle
        window.__inbox.push(command);
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`server command: ${JSON.stringify(command)}`);
    }

    dispatchToReduxStore(command);
};

window.AssistantClient.sendData = (message: AssistantSdkServerAction) => {
    if (process.env.REACT_APP_ENVIRONMENT === 'development') {
        window.AssistantClient.onRequestState();
    }

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log(`sendData: ${JSON.stringify(message)}`);
    }

    if (window.AssistantHost && typeof window.AssistantHost.sendData === 'function') {
        window.AssistantHost.sendData(JSON.stringify(message), null);
    }
};

window.AssistantClient.onRequestState = () => {
    const state = castFromStore();
    const stateString = JSON.stringify(state);

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke request state callback', stateString);
    }

    if (window.AssistantHost && typeof window.AssistantHost.updateState === 'function') {
        window.AssistantHost.updateState(stateString);
    }

    return state;
};

window.AssistantClient.onRequestRecoveryState = () => {
    const state = store.getState();

    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('invoke request recovery state callback', JSON.stringify(state, null, 2));
    }

    return state;
};

window.AssistantClient.close = () => {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('call close native method');
    }

    if (window.AssistantHost && typeof window.AssistantHost.close === 'function') {
        window.AssistantHost.close();
    }
};

if (window.AssistantHost && typeof window.AssistantHost.ready === 'function') {
    if (process.env.REACT_APP_ENVIRONMENT !== 'production') {
        console.log('call ready native method');
    }

    window.AssistantHost.ready();
}

// эмуляция первой команды от бэка
if (process.env.REACT_APP_ENVIRONMENT === 'development') {
    setTimeout(() => {
        window.AssistantClient.onData({
            type: 'smart_app_data',
            // eslint-disable-next-line @typescript-eslint/camelcase
            smart_app_data: GO_TO_CATALOG_POOR,
        });
    }, 100);
}
