import { addressFullInputAction, addressFullSuccessAction } from 'store/actions/native';
import { AppThunkAction } from 'store/constants';
import { goBack, push, replace } from 'connected-react-router';
import { AddressFullInputCommandPayload } from 'store/constants/native';
import { ROUTES } from 'store/constants/router';
import { selectPathname } from 'store/selectors';
import { getWaitNextNavigationEventPromise } from 'tools/history';

export const addressFullValidationOkThunk = (): AppThunkAction => (dispatch) => {
    dispatch(goBack());
};

export const addressFullInputThunk = (payload: AddressFullInputCommandPayload): AppThunkAction => async (
    dispatch,
    getState,
) => {
    const state = getState();
    const currentRoute = selectPathname(state);

    dispatch(addressFullInputAction(payload));

    switch (currentRoute) {
        case ROUTES.ADDRESS_FULL_PHONE_GUIDE: {
            dispatch(push(ROUTES.ADDRESS_FULL_ACCOMMODATION_GUIDE));
            break;
        }
        case ROUTES.ADDRESS_FULL_ACCOMMODATION_GUIDE: {
            dispatch(goBack());

            await getWaitNextNavigationEventPromise();

            dispatch(replace(ROUTES.ADDRESS_FULL_ALL));

            break;
        }
        default: {
            break;
        }
    }
};

export const addressFullSuccessThunk = (): AppThunkAction => (dispatch) => {
    dispatch(addressFullSuccessAction());

    dispatch(goBack());
};
