import React from 'react';
import styled from 'styled-components';
import { ButtonViews, Button, ButtonSize, ButtonProps } from 'components/Button/Button';
import { HeadlineText1 } from 'components/Text/Headline';
import { BodyText1 } from 'components/Text/Body';
import { color } from 'style/const/color';
import { layout } from 'style/const/layout';

const StyledContainer = styled.div`
    position: relative;

    height: ${layout.contentHeightPx}px;
    padding-left: 128px;
`;

const StyledContent = styled.div`
    position: relative;
    width: 816px;
    height: ${layout.contentHeightPx}px;
    z-index: 2;

    display: flex;
    flex-direction: column;
    justify-content: center;

    align-content: left;
`;

export const DialogTitle = styled(HeadlineText1)`
    color: ${color.text.primary};
`;

export const DialogText = styled(BodyText1)`
    margin-top: 24px;

    min-height: 72px;

    white-space: pre-wrap;

    color: ${color.text.secondary};
`;

const StyledButtonContainer = styled.div`
    margin-left: -24px;
    margin-top: 64px;

    display: flex;
    flex-direction: row;
`;

const StyledButton = styled(Button)`
    margin: 0 24px;

    padding: 20px 32px;
`;

const StyledDecorations = styled.div`
    position: absolute;
    top: 0;
    left: 0;
    z-index: 1;

    width: 100%;
    height: 100%;
`;

export interface DialogActionProps {
    shouldFocusOnMount?: boolean;
    buttonText: string;
    buttonView: ButtonViews;
    onClick: (event: React.SyntheticEvent<HTMLButtonElement>) => void;
}

interface DialogPageProps {
    buttons?: React.ReactNode;
    decorations?: React.ReactNode;
}

export const DialogPage: React.FC<DialogPageProps> = ({ children, buttons, decorations, ...props }) => {
    return (
        <StyledContainer {...props}>
            <StyledContent>
                {children}
                {buttons && <StyledButtonContainer>{buttons}</StyledButtonContainer>}
            </StyledContent>
            <StyledDecorations>{decorations}</StyledDecorations>
        </StyledContainer>
    );
};

export const DialogButton: React.FC<ButtonProps> = ({
    children,
    size = ButtonSize.s,
    view = ButtonViews.secondary,
    ...props
}) => (
    <StyledButton size={size} view={view} {...props}>
        {children}
    </StyledButton>
);

export const FullscreenDecoration = styled.div`
    position: absolute;
    top: ${-1 * layout.headerHeightPx}px;
    left: 0;

    width: ${layout.widthPx}px;
    height: ${layout.heightPx}px;

    background-size: contain;
    background-repeat: no-repeat;
`;
