import { Button, ButtonViews } from 'components/Button/Button';
import { Input } from 'components/Input/Input';
import { PlasmaIcon } from 'components/PlasmaIcon/PlasmaIcon';
import { BodyText1 } from 'components/Text/Body';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import React from 'react';
import { addressFullSubmitAction } from 'store/actions/server';
import { ROUTES } from 'store/constants/router';
import styled from 'styled-components';
import { noop } from 'tools/noop';

const StyledFirstRow = styled.div`
    grid-area: first-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1.4fr 1fr;
    color: #fff;
`;

const StyledSecondRow = styled.div`
    grid-area: second-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1fr 1fr 1fr;
    color: #fff;
`;

const StyledThirdRow = styled.div`
    grid-area: third-row;
    display: grid;
    grid-column-gap: 32px;
    grid-template-columns: 1.4fr 1fr;
    color: #fff;
`;

const StyledFourthRow = styled.div`
    grid-area: fourth-row;
    display: flex;
`;

const SubmitButton = styled(Button)`
    flex-grow: 0;
    padding: 32px 40px;

    &[disabled] {
        opacity: 0.3;
    }
`;

const StyledRoot = styled.div`
    display: grid;
    padding: 82px 128px 0;
    box-sizing: border-box;
    grid-row-gap: 48px;
    grid-column-gap: 32px;
    grid-template-areas: 'first-row' 'second-row' 'third-row' 'fourth-row';

    will-change: transform;
`;

const StyledVoiceHint = styled(BodyText1)`
    display: flex;
    margin-left: 48px;
    align-items: center;

    background: linear-gradient(to right, #00ffdf, #00ff5a);
    color: transparent;
    -webkit-background-clip: text;
`;

const StyledPlasmaIcon = styled(PlasmaIcon)`
    height: 88px;
    width: 88px;
    border: solid 22px transparent;
    box-sizing: border-box;
`;

export const AddressFullAll: React.FC = () => {
    const dispatch = useDispatch();
    const {
        addressInputValue,
        phoneNumberInputValue,
        apartmentInputValue,
        entranceInputValue,
        floorInputValue,
        doorCodeInputValue,
        isSubmitButtonDisabled,
        nextFocus,
        isPhoneValid,
    } = useSelector(({ addressFullPage }) => addressFullPage);

    const handleAddressKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_SIMPLE_GUIDE));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handlePhoneKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_FULL_PHONE));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleApartmentKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_FULL_APARTMENT));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleEntranceKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_FULL_ENTRANCE));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleFloorKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_FULL_FLOOR));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleDoorCodeKeyPress = useDebouncedOnceFn(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            e.preventDefault();
            dispatch(push(ROUTES.ADDRESS_FULL_DOOR_CODE));
        },
        debounceTimeoutMs,
        [dispatch],
    );
    const handleSubmitClick = useDebouncedOnceFn(
        () => {
            dispatch(addressFullSubmitAction());
        },
        debounceTimeoutMs,
        [dispatch],
    );

    return (
        <StyledRoot>
            <StyledFirstRow>
                <Input
                    title="Улица и дом"
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleAddressKeyPress}
                    value={addressInputValue}
                />
                <Input
                    title="Номер телефона"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_PHONE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handlePhoneKeyPress}
                    value={phoneNumberInputValue}
                    isInvalid={!isPhoneValid}
                />
            </StyledFirstRow>
            <StyledSecondRow>
                <Input
                    title="Квартира"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_APARTMENT}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleApartmentKeyPress}
                    value={apartmentInputValue}
                />
                <Input
                    title="Подъезд"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ENTRANCE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleEntranceKeyPress}
                    value={entranceInputValue}
                />
                <Input
                    title="Этаж"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_FLOOR}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleFloorKeyPress}
                    value={floorInputValue}
                />
            </StyledSecondRow>
            <StyledThirdRow>
                <Input
                    title="Комментарий"
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_DOOR_CODE}
                    onChange={noop} // реакт запрещает прокидывать в инпут значение без обработчика изменений
                    onRootKeyPress={handleDoorCodeKeyPress}
                    value={doorCodeInputValue}
                />
            </StyledThirdRow>
            <StyledFourthRow>
                <SubmitButton
                    view={isSubmitButtonDisabled ? ButtonViews.secondary : ButtonViews.primary}
                    shouldFocusOnMount={nextFocus === ROUTES.ADDRESS_FULL_ALL}
                    disabled={isSubmitButtonDisabled}
                    shouldLockAfterClick
                    onClick={handleSubmitClick}
                >
                    Сохранить
                </SubmitButton>
                <StyledVoiceHint>
                    <StyledPlasmaIcon fill="url(#plasma-green-gradient)">
                        <linearGradient id="plasma-green-gradient">
                            <stop offset="0%" stopColor="#00ffdf" />
                            <stop offset="100%" stopColor="#00ff5a" />
                        </linearGradient>
                    </StyledPlasmaIcon>
                    Вы можете изменять данные голосом
                </StyledVoiceHint>
            </StyledFourthRow>
        </StyledRoot>
    );
};
