import React from 'react';

export interface MultilineTextProps {
    text: string;
}

export const MultilineText: React.FC<MultilineTextProps> = ({ text }) => {
    const lineList = text.split('\n').reduce((nodeList, line, index) => {
        if (index) {
            nodeList.push(<br key={`${index}br`} />);
        }

        nodeList.push(line);

        return nodeList;
    }, [] as React.ReactNode[]);

    return <>{lineList}</>;
};
