import React from 'react';
import styled, { css } from 'styled-components';
import { useFocusOnMount } from 'hooks/useFocusOnMount';
import { font } from 'style/const/font';
import { transition } from 'style/const/transition';
import { color } from 'style/const/color';

interface StyledTitleProps {
    isMinified?: boolean;
    isFocused?: boolean;
    isInvalid?: boolean;
}
const StyledTitle = styled.div<StyledTitleProps>`
    position: absolute;
    left: 32px;
    top: 36px;

    font-weight: 500;
    font-size: 32px;
    line-height: 40px;

    color: ${({ isInvalid, isFocused }) => {
        if (isInvalid) {
            return color.text.critical;
        }

        if (isFocused) {
            return color.text.active;
        }

        return color.text.tertiary;
    }};

    transition: font-size 0.2s ease-in-out, top 0.2s ease-in-out, color 0.2s ease-in-out;

    ${({ isMinified }) =>
        isMinified &&
        css`
            top: 12px;
            font-size: 24px;
        `};
`;

interface StyledInputProps {
    disabled?: boolean;
}
const StyledInput = styled.input<StyledInputProps>`
    margin-top: 50px;
    height: 40px;
    width: 100%;
    font-family: inherit;
    font-weight: ${font.weight.medium};
    font-size: 32px;
    color: ${({ disabled }) => (disabled ? 'rgba(255, 255, 255, 0.28)' : '#fff')};
    caret-color: ${color.text.active};
    line-height: 40px;
    padding: 0;
    border: none;
    background: transparent;
`;

const StyledWrapper = styled.div`
    position: relative;
    box-sizing: border-box;
    height: 112px;
    padding: 4px 36px;
    background-color: ${color.surface.liquid1};

    &::after {
        content: '';
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;

        display: block;
        height: 4px;

        background-color: ${color.surface.liquid2};

        pointer-events: none;

        transition: bottom ${transition.default};
    }
`;

interface StyledRootProps {
    isFocused?: boolean;
}
const StyledRoot = styled.div<StyledRootProps>`
    margin: -4px;
    height: 120px;
    box-sizing: border-box;
    border: 4px solid transparent;
    border-radius: 28px;

    overflow: hidden;

    transition: border ${transition.default};

    ${({ isFocused }) =>
        isFocused &&
        css`
            border-color: ${color.text.active};

            ${StyledTitle} {
                transition: color ${transition.default};
            }

            ${StyledWrapper}::after {
                bottom: -4px;
            }
        `}
`;

interface InputProps {
    title: string;
    value: string;
    className?: string;
    type?: string;
    disabled?: boolean;
    isInvalid?: boolean;
    onSubmit?: (event: React.SyntheticEvent<HTMLInputElement>) => void;
    onChange?: (event: React.ChangeEvent<HTMLInputElement>) => void;
    onRootKeyPress?: (event: React.KeyboardEvent<HTMLInputElement>) => void;
    forwardRef?: React.RefObject<HTMLDivElement>;
    forwardInputRef?: React.RefObject<HTMLInputElement>;
    shouldFocusOnMount?: boolean;
    shouldFocusInputOnMount?: boolean;
}

export const Input: React.FC<InputProps> = ({
    title,
    value,
    className,
    type = 'text',
    disabled,
    isInvalid,
    onSubmit,
    onChange,
    onRootKeyPress,
    forwardRef,
    forwardInputRef,
    shouldFocusOnMount,
    shouldFocusInputOnMount,
}) => {
    const [isFocused, setFocus] = React.useState(false);
    const [isUsing, setUsing] = React.useState(false);
    const inputRootRef = React.useRef<HTMLInputElement>(null);
    const inputRef = forwardInputRef || inputRootRef;
    const onMountRootRef = useFocusOnMount<HTMLDivElement>(forwardRef);
    const rootRef = shouldFocusOnMount ? onMountRootRef : forwardRef;
    const handleRootFocus = React.useCallback(() => setFocus(true), [setFocus]);
    const handleRootBlur = React.useCallback(() => {
        setFocus(false);
        setUsing(false);
    }, [setFocus, setUsing]);
    const handleRootKeyPress = React.useCallback(
        (e: React.KeyboardEvent<HTMLInputElement>) => {
            if (inputRef && inputRef.current) {
                if (onRootKeyPress) {
                    onRootKeyPress(e);
                }

                if (!e.defaultPrevented) {
                    inputRef.current.focus();
                    setUsing(true);
                }
            }
        },
        [onRootKeyPress, setUsing, inputRef],
    );
    const handleInputKeyPress = React.useCallback(
        (event: React.KeyboardEvent<HTMLInputElement>) => {
            if (onSubmit && event && event.key === 'Enter') {
                event.stopPropagation();
                onSubmit(event);
            }
        },
        [onSubmit],
    );

    React.useEffect(() => {
        if (shouldFocusInputOnMount && inputRef && inputRef.current) {
            inputRef.current.focus();
            setUsing(true);
        }
    }, [shouldFocusInputOnMount, inputRef, setUsing]);

    const isTitleMinified = isUsing || Boolean(value);

    return (
        <StyledRoot
            tabIndex={disabled ? -1 : 0}
            className={className}
            ref={rootRef}
            isFocused={isFocused}
            onFocus={handleRootFocus}
            onBlur={handleRootBlur}
            onKeyPress={handleRootKeyPress}
        >
            <StyledWrapper>
                <StyledTitle isMinified={isTitleMinified} isFocused={isFocused} isInvalid={isInvalid}>
                    {title}
                </StyledTitle>
                <StyledInput
                    tabIndex={-1}
                    ref={inputRef}
                    type={type}
                    disabled={disabled}
                    onKeyPress={handleInputKeyPress}
                    onChange={onChange}
                    value={value}
                />
            </StyledWrapper>
        </StyledRoot>
    );
};
