import { getCountById, getPriceById, getQuantityLeftById } from 'store/reducers/helpers/catalogPage';
import { selectCartItemById, selectProductById } from 'store/selectors';
import { AppReducer, AppState, initialState } from 'store/constants';

const getTotalCount = (state: AppState) =>
    Object.values(state.cart.itemsMap).reduce((acc, { itemId }) => {
        acc += getCountById(state, itemId);

        return acc;
    }, 0);

const getTotalPrice = (state: AppState) =>
    state.cart.order.reduce((acc, itemId) => {
        const count = getCountById(state, itemId);
        const price = getPriceById(state, itemId);

        acc += count * price;

        return acc;
    }, 0);

export const cartModel: AppReducer = (state, action) => {
    switch (action.type) {
        case 'ADD_ITEM': {
            const { count: countString, item: itemId } = action.payload;
            const count = Number(countString);
            const productItem = selectProductById(state, itemId);
            const quantityLeft = getQuantityLeftById(state, itemId);

            if (!productItem) {
                return;
            }

            const cartItem = selectCartItemById(state, itemId);

            if (cartItem) {
                cartItem.count = Math.min(quantityLeft, cartItem.count + count);
            } else if (count <= quantityLeft) {
                state.cart.itemsMap[itemId] = {
                    itemId,
                    count,
                };

                state.cart.order.push(itemId);
            }

            break;
        }

        case 'REMOVE_ITEM': {
            const { item: itemId, count: countString, shouldRemoveMention } = action.payload;
            const count = Number(countString);

            const cartItem = selectCartItemById(state, itemId);
            const productItem = selectProductById(state, itemId);
            const actualCount = getCountById(state, itemId);

            if (!cartItem || !productItem) {
                return;
            }

            cartItem.count = Math.max(0, actualCount - count);

            if (!cartItem.count && shouldRemoveMention) {
                delete state.cart.itemsMap[itemId];
                state.cart.order = state.cart.order.filter((cartItemId) => cartItemId !== itemId);
            }

            break;
        }

        case 'REMOVE_ALL': {
            state.cart = { ...initialState.cart };

            break;
        }

        case 'GO_TO_CATALOG': {
            if (action.payload.clear) {
                state.cart = { ...initialState.cart };
            }

            break;
        }

        case 'VALIDATION_OK': {
            state.cart = { ...initialState.cart };
            state.cart.orderId = action.payload.order_id;
            state.cart.date = new Date(action.payload.date);

            break;
        }

        default:
            break;
    }

    state.cart.totalCount = getTotalCount(state);
    state.cart.totalPrice = getTotalPrice(state);
};
