import React from 'react';
import styled from 'styled-components';
import { push } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { debounceTimeoutMs, useDebouncedOnceFn } from 'hooks/useDebouncedFn';
import { selectHeader } from 'store/selectors';
import { ROUTES } from 'store/constants/router';
import { HeaderButton } from 'components/HeaderButton/HeaderButton';
import { icon } from 'style/const/icon';

import addressIcon from './address.svg';

const addressIconSizePx = icon.m.sizePx;

const StyledIcon = styled.div`
    width: ${addressIconSizePx}px;
    height: ${addressIconSizePx}px;

    background-image: url('${addressIcon}');
    background-repeat: no-repeat;
    background-size: contain;
`;

interface HeaderAddressProps {
    forwardRef?: React.RefObject<HTMLDivElement>;
    isVisible: boolean;
}

export const HeaderAddress: React.FC<HeaderAddressProps> = (props) => {
    const dispatch = useDispatch();

    const { text } = useSelector((state) => selectHeader(state).address);

    const handleClick = useDebouncedOnceFn(() => dispatch(push(ROUTES.ADDRESS_SIMPLE_GUIDE)), debounceTimeoutMs, [
        dispatch,
    ]);

    return (
        <HeaderButton onClick={handleClick} text={text} {...props}>
            <StyledIcon />
        </HeaderButton>
    );
};
