import React from 'react';
import styled, { css } from 'styled-components';
import { IndexBadge } from 'components/IndexBadge/IndexBadge';
import { BodyText1 } from 'components/Text/Body';
import { Label, LabelProps } from 'components/Label/Label';
import { color } from 'style/const/color';
import { FootnoteText1 } from 'components/Text/Footnote';

const StyledIndexBadge = styled(IndexBadge)`
    top: 4px;
    right: 4px;
`;

const thumbSizePx = 120;

interface StyledThumbProps {
    imageUrl: string;
}

const StyledThumb = styled.div<StyledThumbProps>`
    position: relative;

    margin-right: 32px;

    height: ${thumbSizePx}px;
    width: ${thumbSizePx}px;

    border-radius: 24px;

    background-image: url('${({ imageUrl }) => imageUrl}');
    background-size: contain;
    background-position: center;
    background-repeat: no-repeat;
`;

const StyledInfo = styled.div`
    margin-right: 48px;

    width: 384px;

    display: flex;
    flex-direction: column;
    justify-content: center;
`;

interface StyledNameProps {
    isOneLine?: boolean;
}
const StyledName = styled(BodyText1)<StyledNameProps>`
    overflow: hidden;

    ${({ isOneLine }) =>
        isOneLine &&
        css`
            white-space: nowrap;
            text-overflow: ellipsis;
        `}

    ${({ isOneLine }) =>
        !isOneLine &&
        css`
            display: -webkit-box;
            -webkit-box-orient: vertical;
            -webkit-line-clamp: 2;
        `}

    color: ${color.text.primary};
`;

const StyledPriceContainer = styled.div`
    display: flex;
`;

const StyledPrice = styled(FootnoteText1)`
    margin-top: 4px;

    color: ${color.text.secondary};
`;

const StyledOldPrice = styled(FootnoteText1)`
    position: relative;
    opacity: 0.24;
    color: ${color.text.primary};
    margin-top: 4px;
    margin-left: 8px;

    &::before {
        content: '';
        position: absolute;
        top: calc(50% - 2px);
        border-top: 4px solid ${color.text.primary};
        left: 2px;
        right: 2px;
    }
`;

const StyledLabel = styled(Label)`
    margin-top: 4px;
`;

interface StyledCartListItemProps {
    disabled?: boolean;
}

export const CartListItemRoot = styled.div<StyledCartListItemProps>`
    position: relative;

    box-sizing: border-box;
    height: 152px;
    width: 832px;
    padding: 16px;

    display: flex;
    align-items: center;

    ${({ disabled }) =>
        disabled &&
        css`
            ${StyledThumb},
            ${StyledName},
            ${StyledPrice} {
                opacity: .24;
            }

            ${StyledPrice} {
                color: ${color.text.primary};
            }
        `}
`;
interface CartListItemThumbProps {
    imageUrl: string;
    index?: number;
}

const StyledCount = styled.span`
    color: ${color.text.active};

    &::before {
        content: '×';
    }
`;

export const CartListItemThumb: React.FC<CartListItemThumbProps> = ({ imageUrl, index }) => (
    <StyledThumb imageUrl={imageUrl}>{index !== undefined && <StyledIndexBadge size="s" index={index} />}</StyledThumb>
);

interface CartListItemInfoProps {
    disabled?: boolean;
    label?: LabelProps;
    name: string;
    price: number;
    oldPrice?: number;
    count?: number;
}

export const CartListItemInfo: React.FC<CartListItemInfoProps> = ({
    disabled,
    label,
    name,
    price,
    oldPrice,
    count,
}) => (
    <StyledInfo>
        <StyledName isOneLine={disabled || Boolean(label)}>
            {name}
            &nbsp;
            {count ? <StyledCount>{count}</StyledCount> : null}
        </StyledName>
        <StyledPriceContainer>
            {price ? <StyledPrice>{price} ₽</StyledPrice> : null}
            {oldPrice ? <StyledOldPrice>{oldPrice} ₽</StyledOldPrice> : null}
        </StyledPriceContainer>
        {label ? <StyledLabel {...label} /> : null}
    </StyledInfo>
);
