import { AppReducer } from '../../constants';

export const deliveryData: AppReducer = (state, action) => {
    switch (action.type) {
        case 'GO_TO_CATALOG': {
            state.delivery.deliveryTime = action.payload.time;
            state.delivery.minimalPrice = action.payload.minimal_amount;
            break;
        }

        default:
            break;
    }
};
