import React from 'react';

export const useKeyboardExpanded = () => {
    const [keyboardExpandedCount, setKeyboardExpandedCount] = React.useState(0);

    React.useLayoutEffect(() => {
        const handleWindowResize = () => {
            setKeyboardExpandedCount((currentStateValue) => currentStateValue + 1);
        };

        window.addEventListener('resize', handleWindowResize);

        return () => {
            window.removeEventListener('resize', handleWindowResize);
        };
    }, [setKeyboardExpandedCount]);

    const isKeyboardExpanded = keyboardExpandedCount % 2 !== 0;

    return [isKeyboardExpanded, keyboardExpandedCount];
};
