import React from 'react';
import styled from 'styled-components';
import { goBack } from 'connected-react-router';
import { useDispatch } from 'hooks/useDispatch';
import { useSelector } from 'hooks/useSelector';
import { closeAppAction } from 'store/actions/app';
import {
    DialogPage,
    DialogTitle,
    DialogButton,
    DialogText,
    FullscreenDecoration,
} from 'components/DialogPage/DialogPage';
import { MultilineText } from 'components/MultilineText/MultilineText';

import circlesImage from './Error.assets/circles.svg';
import warningIcon from './Error.assets/warning.svg';

const Circles = styled.div`
    position: absolute;
    top: -280px;
    right: -266px;

    box-sizing: border-box;
    width: 1596px;
    height: 1596px;

    display: flex;
    justify-content: center;
    align-items: center;

    background-image: url('${circlesImage}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

const WarningIcon = styled.div`
    width: 384px;
    height: 384px;

    background-image: url('${warningIcon}');
    background-position: center;
    background-repeat: no-repeat;
    background-size: cover;
`;

export const Error: React.FC = () => {
    const { title, subtitle, retryButtonText, closeButtonText } = useSelector(({ errorPage }) => errorPage);

    const dispatch = useDispatch();

    const buttons = React.useMemo(
        () => (
            <>
                <DialogButton
                    shouldFocusOnMount
                    onClick={() => {
                        dispatch(goBack());
                    }}
                >
                    {retryButtonText}
                </DialogButton>
                {closeButtonText ? (
                    <DialogButton
                        onClick={() => {
                            dispatch(closeAppAction());
                        }}
                    >
                        {closeButtonText}
                    </DialogButton>
                ) : null}
            </>
        ),
        [retryButtonText, closeButtonText, dispatch],
    );

    const decorations = React.useMemo(
        () => (
            <FullscreenDecoration>
                <Circles>
                    <WarningIcon />
                </Circles>
            </FullscreenDecoration>
        ),
        [],
    );

    return (
        <DialogPage buttons={buttons} decorations={decorations}>
            <DialogTitle>
                <MultilineText text={title} />
            </DialogTitle>
            <DialogText>
                <MultilineText text={subtitle} />
            </DialogText>
        </DialogPage>
    );
};
