import React from 'react';
import styled from 'styled-components';
import { Pin, HappyFaceIcon } from 'components/Pin/Pin';
import { color } from 'style/const/color';
import { ParagraphText2 } from 'components/Text/Paragraph';
import { CaptionText } from 'components/Text/Caption';
import { BodyText1 } from 'components/Text/Body';
import { HeadlineText2 } from 'components/Text/Headline';

export const CartInfoContainer = styled.div`
    position: relative;

    width: 744px;
    padding-top: 48px;
    box-sizing: border-box;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledTime = styled(ParagraphText2)`
    color: ${color.text.primary};
`;

const StyledUnit = styled(CaptionText)`
    margin-top: -10px;

    color: ${color.text.primary};
`;

interface CartInfoProps {
    time?: number;
}

export const CartInfoPin: React.FC<CartInfoProps> = ({ time }) => (
    <Pin pinColor={color.button.critical}>
        {time !== undefined ? (
            <>
                <StyledTime>{time}</StyledTime>
                <StyledUnit>мин</StyledUnit>
            </>
        ) : (
            <HappyFaceIcon />
        )}
    </Pin>
);

const StyledPersonal = styled.div`
    margin-top: 8px;

    display: flex;
    flex-direction: column;
    align-items: center;
`;

const StyledPersonalLocation = styled(BodyText1)`
    -webkit-line-clamp: 2;

    color: ${color.text.primary};
`;

const StyledPersonalPhoneNumber = styled(HeadlineText2)`
    margin-top: 60px;

    color: ${color.text.primary};
`;

const StyledPersonalName = styled(BodyText1)`
    margin-top: 8px;

    color: ${color.text.primary};
`;

interface CartPersonalProps {
    address: string;
    phone: string;
    name?: string;
}

export const CartPersonal: React.FC<CartPersonalProps> = ({ address, phone, name }) => (
    <StyledPersonal>
        <StyledPersonalLocation>{address}</StyledPersonalLocation>
        <StyledPersonalPhoneNumber>{phone}</StyledPersonalPhoneNumber>
        {name ? <StyledPersonalName>{name}</StyledPersonalName> : null}
    </StyledPersonal>
);

export const CartAddress = styled(HeadlineText2)`
    margin-top: 48px;

    display: flex;
    flex-direction: column;
    align-items: center;

    -webkit-line-clamp: 2;

    color: ${color.text.primary};
`;
